<template>
    <v-container class="home ma-0 pa-0" fluid v-if="this.verificacion === true">

        <div v-if="this.verificacion === true && this.dataReady === true">
            <barra-superior :titulo="titulo"></barra-superior>
            <div v-if="usuario.primeraVez == true ">
                <v-dialog v-model="usuario.primeraVez" persistent max-width="600px">
                    <v-card>
                        <v-card-title style="background: #05c5ff" class="text-center justify-center">
                            <span class="headline">Primer login</span>
                        </v-card-title>
                        <v-card-text>
                            Como primer login, se te ha asignado un hogar por defecto.<br>
                        </v-card-text>

                        <v-card-actions class="text-center justify-center">
                            <v-spacer></v-spacer>
                            <v-btn style="background: #FF5D5E;  width: 40%" text
                                   @click="funcionClose">Cerrar
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </div>
            <div v-else>
                <v-row class="text-center justify-center;" style="margin-top: 60px" no-gutters justify="space-around"
                       v-if="this.dataReady==true">
                    <v-container v-for="hogar in lista_hogares" :key="hogar.id">
                        <v-card style="padding: 0px">
                            <v-card-title class="text-center justify-center pa-0">
                                {{hogar.apodo}}
                                <div v-if="hogar.estado == false" style="margin-left: 6px!important;">
                                    <v-icon color="red">
                                        mdi-exclamation-thick
                                    </v-icon>
                                </div>
                                <div v-else style="margin-left: 6px!important;">
                                    <v-icon color="green">
                                        mdi-check-outline
                                    </v-icon>
                                </div>
                            </v-card-title>
                            <v-divider></v-divider>
                            <div v-for="sensor in hogar.vectorSensores" :key="sensor.id" style="display:inline-block">
                                <div v-for="item in lista_IdSensores" :key="item.id">
                                    <div class="ma-2" v-if="item.id == sensor">
                                        <v-btn style="margin: 3px" class="mx-2" fab large @click="botonDetalles(item)">
                                            <v-icon style="width: 60%">
                                                mdi-access-point
                                            </v-icon>
                                            <div v-if="item.estado == true">
                                                <v-icon color="green" style="width: 30%">
                                                    mdi-check-outline
                                                </v-icon>
                                            </div>
                                            <div v-else>
                                                <v-icon color="red" style="width: 30%">
                                                    mdi-exclamation-thick
                                                </v-icon>
                                            </div>
                                        </v-btn>
                                        <br>
                                        {{item.apodo}}
                                    </div>
                                </div>
                            </div>
                            <div v-if="hogar.vectorSensores.length == 0">
                                <v-card-text>
                                    Sin sensores asociados aún
                                </v-card-text>
                            </div>
                        </v-card>
                    </v-container>
                    <div v-if="lista_hogares.length == 0" style="margin: 10%">
                        <v-layout v-if="this.dataReady!=false" align-center justify-center style="margin-top: 60px">
                            <v-progress-circular
                                    indeterminate
                                    color="blue"
                                    size="250"
                                    class="mb-0"
                            ></v-progress-circular>
                        </v-layout>
                        <h4 v-else style="margin-top: 60px; background: aquamarine">
                            <div v-if="this.dataReady === true">
                                No hay hogares registrados aún<br>
                                <h4 style="font-weight: bold">Registra un hogar para poder asociar un sensor</h4>
                            </div>
                        </h4>
                    </div>
                </v-row>
                <v-row v-else>
                    <v-layout align-center justify-center style="margin-top: 60px">
                        <v-progress-circular
                                indeterminate
                                color="blue"
                                size="250"
                                class="mb-0"
                        ></v-progress-circular>
                    </v-layout>
                </v-row>
            </div>

        </div>

    </v-container>

    <v-container v-else>
        <div class="mdl-card mdl-shadow--2dp firebaseui-container firebaseui-id-page-email-verification-success">
            <v-card style="margin-top: 20%; background: deepskyblue">
                <h2>
                    Vaya al correo registrado y verifique usuario.
                </h2>
            </v-card>
        </div>
    </v-container>

</template>

<script>
    import * as firebase from 'firebase';
    import {mapActions} from 'vuex';
    import Vue from 'vue'

    Vue.config.devtools = true;
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";


    export default {
        name: 'Home',
        components: {
            BarraSuperior
        },
        data() {
            return {

                dataReady: false,
                titulo: 'Home',
                home: {
                    title: 'Home',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHome'
                },
                hogares: {
                    title: 'Hogares',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHogares'
                },
                dispositivos: {
                    title: 'Dispositivos',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionDispositivos'
                },
                alertas: {
                    title: 'Alertas',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionAlertas'
                },

                verificacion: null,
                datosCargados: false,
                mini: true,
                drawer: true,
                lista_hogares: null,
                lista_IdSensores: [],

                usuario:
                    {
                        dato1: null,
                        dato2: null,
                        email: null,
                        password: null,
                        primeraVez: null,
                        apellido: null,
                        nombre: null,
                        tokens: null,
                        foto: null,
                        db: firebase.firestore()
                    }
                ,
            }
        },
        filters: {
            currencydecimal(value) {
                return value.toFixed(2)
            }
        },
        watch: {

            // @vuese
            //Comprueba que el email esté verificado
            //@arg Valor donde se guarda si el email está verificado
            verificacion: function (value) {
                if (value == true) {
                    console.log("FORZAMOS");
                } else {
                    console.log("NO FORZAMOS")
                }
            },

            // @vuese
            // Muestra la lista de hogares de un ususario y escucha cualqueir cambio de estado en sus respectivos dispositivos
            lista_hogares: function () {
                let self = this;
                self.lista_hogares.forEach((h) => {
                    let hogarEstado = true;
                    let sindex = -1;
                    h.vectorSensores.forEach(sid => {
                        sindex = self.lista_IdSensores.findIndex(s => {
                            return s.identificacion == sid;
                        });

                        hogarEstado = (hogarEstado && self.lista_IdSensores[sindex].estado);
                    });

                    h.estado = hogarEstado;

                });
            }
        },

        created: async function () {
            let db = firebase.firestore();
            let usuarioID = firebase.auth().currentUser.uid;
            let self = this;
            if (usuarioID == 'Cf3bZ48IqxgS3iktsXy2HvH1LDx2') {
                self.verificacion = true;
            } else {
                self.verificacion = firebase.auth().currentUser.emailVerified;
            }
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre);
                self.usuario.email = (doc.data().email);
                self.usuario.apellido = (doc.data().apellido);
                self.usuario.primeraVez = (doc.data().primeraVez);
                self.usuario.password = (doc.data().password);
                self.usuario.foto = (doc.data().foto);
                self.usuario.tokens = (doc.data().tokens);
                console.log("Nombre: " + self.usuario.nombre)
            });
            console.log(usuarioID)
            //Ahora habria que coger los sensores del usuario
            await db.collection("Dispositivos").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                self.lista_IdSensores = [];
                self.lista_hogares = [];
                querySnapshot.forEach(function (doc) {
                    let sensor = {
                        id: doc.id,
                        ...doc.data()
                    };
                    self.lista_IdSensores.push(sensor);

                });

                //Cojo los hogares del usuario lo primero
                db.collection("Hogares").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                    self.lista_hogares = [];
                    querySnapshot.forEach(function (doc) {
                        let hogar = {
                            id: doc.id,
                            ...doc.data()
                        };
                        self.lista_hogares.push(hogar);
                    });
                });

            });
            if (this.verificacion == false) {
                setInterval(this.comprobar, 3000);
            }

            console.log("1--> " + this.verificacion)


            this.dataReady = true;


        },


        methods: {


            ...mapActions(
                ['guardarUsuario']
            ),


            // @vuese
            //Comprueba si el email a sido verificado
            comprobar: async function () {
                // eslint-disable-next-line no-console
                let usuarioID = firebase.auth().currentUser;
                // eslint-disable-next-line no-console
                if (usuarioID != null) {
                    // eslint-disable-next-line no-console
                    usuarioID.reload();
                }
                // eslint-disable-next-line no-console
                if (usuarioID != null) {
                    // eslint-disable-next-line no-console
                    this.verificacion = firebase.auth().currentUser.emailVerified;
                }
            },


            // @vuese
            //Añade el hogar por defecto de la primera vez que se hace login y pide los datos y permisos necesarios
            funcionClose: function () {
                this.usuario.primeraVez = false;
                //Primero creo un hogar por defecto para el usuario
                let hogarRef = firebase.firestore().collection("Hogares").doc();
                hogarRef.set({
                    idHogar: hogarRef.id,
                    direccion: null,
                    apodo: 'Hogar por defecto ',
                    usuarioAsociado: firebase.auth().currentUser.uid,
                    vectorSensores: []
                });
                console.log(hogarRef.id);

                firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                    primeraVez: false
                }, {merge: true}).then(function () {
                }).catch(function (error) {
                    console.error("Error adding document: ", error);
                });

                console.log("pedir notificacion");
                let self = this;
                var flag = true;
                var i = 0;
                const messaging = firebase.messaging();
                messaging.usePublicVapidKey("BAzdi2QjOv1jdumjVOAmC0SBts9ukMpmWEMGk2iIF12m672JJ7W8LryLRBRitvtC_iBPMcg0w7gEU9IHaMblR5s"); // 1. Generate a new key pair
                messaging.requestPermission().then(() => {
                    // eslint-disable-next-line no-console
                    console.log('Notification permission granted.');
                    // Get Token
                    messaging.getToken().then((token) => {
                        // eslint-disable-next-line no-console
                        console.log(token);
                        console.log(self.usuario.tokens.length);
                        console.log("--" + self.usuario.tokens);
                        for (i = 0; i < self.usuario.tokens.length; i++) {
                            if (self.usuario.tokens[i] == token) {
                                flag = false
                            }
                        }
                        if (flag == true) {
                            self.usuario.tokens.push(token);
                            firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).update({
                                tokens: firebase.firestore.FieldValue.arrayUnion(token)
                            }).then(function () {
                                console.log("escrito")
                            }).catch(function (error) {
                                console.error("Error adding document: ", error);
                            });
                        }
                        this.token = token;
                        Vue.prototype.$appName = token;
                    })

                }).catch((err) => {
                    // eslint-disable-next-line no-console
                    console.log('Unable to get permission to notify.', err);
                });

            },

            // @vuese
            //redirecciona a mostrar los detalles del dispositivo seleccionado
            //@arg dispositivo a mostrar
            botonDetalles: function (dispositivo) {
                console.log("pulsado boton detalles " + dispositivo.apodo)
                const parametro = dispositivo.identificacion
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.push({
                            name: 'DetallesDispositivo',
                            params: {idSensor: parametro, apodo: dispositivo.apodo, origen: "home"}
                        })
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: ' + err.message)

                        console.log('Opps ' + err.message);
                    }
                );

            },

        },


    }
</script>
