import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)

export default new Vuex.Store({
    //state:estado del store, donde guardas los datos
    //getters: para coger los datos, como en java
    //mutations:como los getters pero para realizar funcionalidad. para modificar los datos que stan en el state(los setters)
    //actions: a lo que deberia llamar desde el resto del codigo, que tinen funciones asincronicas y desde donde comiteo los mutations

    state: {
        usuario:{
            nombre:null,
            email:null,
            apellido:null,
            telefono:null,
            foto:null,
        }
    },
    getters: {
        getUsuario:(state)=>{
            return state.usuario;
        }



    },
    mutations: {
        setUsuarioNombre(state,nombre){
           state.usuario.nombre = nombre;
        },
    },
    actions: {
        guardarUsuario(context,usuario){
            context.commit('setUsuarioNombre',usuario.nombre)
        }
    }

});

