<template>
    <div class="login ma-0 pa-0" align="center">
        <div style="margin: 10px">
            <div align="center" style="padding-top: 30px">
                <v-img src="/img/icons/RobertoLogo.png" style="width: 70%; max-width: 400px"></v-img>
            </div>

            <h3>Iniciar sesión</h3>
            <v-form style="margin: 10px;max-width: 600px" align="center">
                <v-text-field
                        ref="email"
                        v-model="email"
                        label="Correo"
                        :rules="[rules.requiredEmail, rules.email]"
                        counter
                        outlined
                        clearable
                ></v-text-field>

                <br>

                <v-text-field
                        :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.requiredContrasena, rules.min]"
                        :type="show3 ? 'text' : 'password'"
                        label="Contraseña"
                        class="input-group--focused"
                        ref="contrasena"
                        v-model="contrasena"
                        counter
                        outlined
                        @click:append="show3 = !show3"
                ></v-text-field>

            </v-form>

            <p>
                <router-link to="/RecuperarContraseña">¿Has olvidado la contraseña?</router-link>
            </p>
            <br>
            <v-btn @click="login" ref="login" rounded color="primary" style="color: aqua; background: red">Iniciar sesión</v-btn>

            <br>

            <h4>¿No tienes una cuenta? Puedes crearte una pulsando
                <router-link to="/sign-up">aquí</router-link>
            </h4>
        </div>
    </div>
</template>

<script>

    import firebase from 'firebase'
    import Vue from "vue";

    export default {
        name: "Login",
        data() {
            return {
                email: '',
                pattern: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                pulsadoLogin: false,
                contrasena: '',
                show3: false,
                emailValido: true,
                contrasenaValido: true,
                usuario:
                    {
                        dato1: null,
                        dato2: null,
                        email: null,
                        password: null,
                        apellido: null,
                        tokens: null,
                        nombre: null,
                        foto: null,
                        db: firebase.firestore()
                    }
                ,
                rules: {
                    requiredEmail: () => this.emailRequerido() || 'Campo requerido.',
                    requiredContrasena: () => this.contrasenaRequerido() || 'Campo requerido.',
                    min: () => this.contrasenaMinimo8() || 'Mínimo 8 carácteres',
                    email: () => {
                        return this.emailFormatoValido() || 'Email no válido.'
                    },

                },

            }
        },
        watch: {
            // @vuese
            // Comprueba que el formato del email sea correcto
            email: function () {
                if (this.email && this.email.length > 0 && this.pattern.test(this.email)) {
                    this.emailValido = true;
                }
            },
            // @vuese
            // Comprueba que el formato de la contraseña sea correcto
            contrasena: function () {
                if (this.contrasena && this.contrasena.length >= 8) {
                    this.contrasenaValido = true;
                }
            }
        },

        created: function () {


            this.email = '';
                this.contrasena = '';
        },
        methods: {

            // @vuese
            //Comprueba que el email no falte a la hora de logearse
            emailRequerido: function () {
                if (this.emailValido) {
                    return true;
                } else {
                    if(this.email != null && this.email.length > 0){
                        return true;
                    }
                    else{
                        console.log("emailr")
                        return false;
                    }
                }
            },

            // @vuese
            //Comprueba que la contraseña no falte a la hora de logearse
            contrasenaRequerido: function () {
                if (this.contrasenaValido) {
                    return true;
                } else {
                    let valid = this.contrasena != null && this.contrasena.length > 0;
                    return valid;
                }
            },

            // @vuese
            //Comprueba que la contraseña sea de mínimo 8 carácteres
            contrasenaMinimo8: function () {
                if (this.contrasenaValido) {
                    return true;
                } else {
                    return this.contrasena.length >= 8;
                }
            },

            // @vuese
            //Comprueba que el email tenga un formato válido
            emailFormatoValido: function () {
                if (this.emailValido) {
                    return true;
                } else {
                    return this.pattern.test(this.email);
                }
            },

            // @vuese
            //Logea al usuario segun los datos introducidos
            login: async function () {

                if (!this.email || this.email.length == 0 || !this.pattern.test(this.email)) {
                    this.emailValido = false;
                }
                if (!this.contrasena || this.contrasena.length < 8) {
                    this.contrasenaValido = false;
                }
                if (!this.emailValido || !this.contrasenaValido) {
                    this.$refs.email.validate();
                    this.$refs.contrasena.validate();
                    return;
                }

                this.pulsadoLogin = true;
                let self = this;
                await firebase.auth().signInWithEmailAndPassword(this.email, this.contrasena).then(
                    async (user) => {
                        console.log(firebase.auth().currentUser.uid);
                         await firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).get().then(function (doc) {
                            self.usuario.nombre = (doc.data().nombre)
                            self.usuario.email = (doc.data().email)
                            self.usuario.apellido = (doc.data().apellido)
                            //self.usuario.telefono = (doc.data().telefono)
                            self.usuario.password = (doc.data().password)
                            self.usuario.foto = (doc.data().foto);
                            self.usuario.tokens = (doc.data().tokens);
                            console.log("Nombre: " + self.usuario.nombre);


                            //Deberia coger el token del usuario
                            if (firebase.auth().currentUser) {
                                if (Vue.prototype.$appName != undefined) {
                                    console.log("eo que estoy aqui" + Vue.prototype.$appName);
                                    console.log("EL TOKEN YA EXISTE, se añade")
                                    self.usuario.tokens.push(Vue.prototype.$appName);
                                    firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).update({
                                        //tokens : self.usuario.tokens
                                        tokens: firebase.firestore.FieldValue.arrayUnion(Vue.prototype.$appName)
                                    }).then(function () {
                                        console.log("escrito")
                                    }).catch(function (error) {
                                        console.error("Error adding document: ", error);
                                    });
                                    self.$router.replace('home');
                                    // eslint-disable-next-line no-console
                                    console.log(user);
                                    //deberia coger los datos del usuario

                                } else {
                                    //si esta identificado se pide permiso para notificar
                                    try {
                                        console.log("EL TOKEN NO EXISTE,pedir notificacion");
                                        var flag = true;
                                        var i = 0;
                                        const messaging = firebase.messaging();
                                        messaging.usePublicVapidKey("BAzdi2QjOv1jdumjVOAmC0SBts9ukMpmWEMGk2iIF12m672JJ7W8LryLRBRitvtC_iBPMcg0w7gEU9IHaMblR5s"); // 1. Generate a new key pair
                                        messaging.requestPermission().then(() => {
                                            // eslint-disable-next-line no-console
                                            console.log('Notification permission granted.');
                                            // Get Token
                                            messaging.getToken().then((token) => {
                                                // eslint-disable-next-line no-console
                                                console.log(token);
                                                console.log(self.usuario.tokens.length);
                                                console.log("--" + self.usuario.tokens);
                                                for (i = 0; i < self.usuario.tokens.length; i++) {
                                                    if (self.usuario.tokens[i] == token) {
                                                        flag = false
                                                    }
                                                }
                                                if (flag == true) {//si es true es que no existe el token y se añade
                                                    console.log("El token no existe, se añade")
                                                    self.usuario.tokens.push(token);
                                                    firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).update({
                                                        //tokens : self.usuario.tokens
                                                        tokens: firebase.firestore.FieldValue.arrayUnion(token)
                                                    }).then(function () {
                                                        console.log("escrito")
                                                    }).catch(function (error) {
                                                        console.error("Error adding document: ", error);
                                                    });
                                                }
                                                Vue.prototype.$appName = token;
                                                self.$router.replace('home');
                                                // eslint-disable-next-line no-console
                                                console.log(user);
                                                //deberia coger los datos del usuario
                                            })

                                        }).catch((err) => {
                                            Vue.prototype.$appName = undefined;
                                            // eslint-disable-next-line no-console
                                            console.log('Unable to get permission to notify.Vamos, que no quiere que le turres', err);
                                            self.$router.replace('home');
                                            // eslint-disable-next-line no-console
                                            console.log(user);
                                            //deberia coger los datos del usuario
                                        });


                                    }catch (err) {
                                        console.error(err)
                                        self.$router.replace('home');
                                    }

                                }

                            }
                        });
                    },
                    function (err) {
                        alert('Error: ' + 'El ususario no existe o los datos están mal introducidos')
                        // eslint-disable-next-line no-console
                        console.log('Opps ' + err.message);
                    }
                );
            }
        }
    }
</script>

<style scoped>


</style>
