firebase.auth().onAuthStateChanged(()=>{//aqui iniciomos la app soloc uando el firebase auth este lsito para usarse
  if(!app){
    app= new Vue({
      el:"#app",
      components:{App},
      template:"<App/>",
      router,
      store,
      vuetify,
      render:h=>h(App)
    }).$mount('#app');
  }
})
