<template>
    <v-container class="Alertas  ma-0 pa-0 text-center justify-center" fluid>
        <barra-superior :titulo="titulo"></barra-superior>
        <v-row no-gutters justify="space-around" class="text-center justify-center" v-if="dataReady === true">
            <v-col class="text-center justify-center">
                <div class="text-center justify-center"
                     style="visibility: hidden; left: 8px; top: 56px; display: none;"></div>
                <v-col style="margin-bottom: 80px">
                    <v-card-title class="headline justify-center">ALERTAS</v-card-title>
                    <v-container class=" text-center justify-center" fluid v-for="alerta in lista_alertas.slice(0,20)"
                                 :key="alerta.id">
                        <v-row dense style="width: auto">
                            <v-card raised style="margin: auto; width: 100%; text-align: left; max-width: 600px">
                                <div v-if="alerta.formato === '%'">
                                    <v-card-title style="font-weight: bold" class="headline">
                                        <v-icon color="orange">
                                            mdi-water-outline
                                        </v-icon>
                                        {{parseInt(alerta.tempActual)}}{{alerta.formato}} de humedad
                                    </v-card-title>
                                </div>
                                <div v-else-if="alerta.formato === 'C'">
                                    <v-card-title style="font-weight: bold" class="headline">
                                        <v-icon color="orange">
                                            mdi-thermometer-alert
                                        </v-icon>
                                        {{parseInt(alerta.tempActual)}}º{{alerta.formato}} de temperatura
                                    </v-card-title>
                                </div>
                                <div v-else-if="alerta.formato === 'F'">
                                    <v-card-title style="font-weight: bold" class="headline">
                                        <v-icon color="orange">
                                            mdi-thermometer-alert
                                        </v-icon>
                                        {{parseInt(alerta.tempActual)}}º{{alerta.formato}} de temperatura
                                    </v-card-title>
                                </div>
                                <div v-else>
                                    <v-card-title style="font-weight: bold" class="headline">
                                        <v-icon color="orange">
                                            mdi-cloud-alert
                                        </v-icon>
                                        {{parseInt(alerta.tempActual)}} {{alerta.formato}}
                                    </v-card-title>
                                </div>

                                <v-card-subtitle>{{alerta.mensaje}}</v-card-subtitle>
                                <v-card-text>{{new Date(alerta.time).getHours()}}:{{new
                                    Date(alerta.time).getMinutes()}}, {{new
                                    Date(alerta.time).toLocaleDateString("es-ES",{ year:"numeric", month: "long",
                                    day:"numeric"})}}
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn style="background: #FF5D5E " text @click="botonBorrarAbrirDialog(alerta)">
                                        Borrar
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-row>
                    </v-container>

                    <v-btn fixed fab right bottom color="primary" dark @click="borrarAlarmasAbrirDialog"
                           :disabled="!existenAlertas">
                        <v-icon>mdi-delete</v-icon>
                    </v-btn>

                    <h4 class="text-center justify-center" style="margin-top: 10%; background: aquamarine"
                        v-if="lista_alertas.length == 0">
                        Sin resgitros<br>
                        <h4 style="font-weight: bold">No se ha detectado ningun valor peligroso</h4>
                    </h4>


                </v-col>
            </v-col>
        </v-row>
        <v-row v-else>
            <v-layout align-center justify-center style="margin-top: 60px">
                <v-progress-circular
                        indeterminate
                        color="blue"
                        size="250"
                        class="mb-0"
                ></v-progress-circular>
            </v-layout>
        </v-row>
        <!--Dialog borrar-->
        <v-dialog v-model="dialogBorrarUna" persistent max-width="500px">
            <v-card>
                <v-card-title class="text-center justify-center" v-if="this.dispositivoBorrar!=null"
                              style="background: lightskyblue; width: auto">
                    ¿Estás seguro de borrar la <br>notificación de {{this.dispositivoBorrar.apodo}}?
                </v-card-title>

                <v-btn @click="funcionClose" style="margin: 2%;width: 45%;background: #FF5D5E">
                    Calcelar
                </v-btn>
                <v-btn style="margin: 2%;width: 45%;background: skyblue" @click="botonBorrar(dispositivoBorrar)">
                    Si
                </v-btn>
            </v-card>
        </v-dialog>
        <!--Dialog borrar-->
        <v-dialog v-model="dialogBorrarTodas" persistent max-width="500px">
            <v-card>
                <v-card-title class="text-center justify-center" style="background: lightskyblue">
                    ¿Estás seguro de borrar todas las notificaciones?
                </v-card-title>
                <v-btn @click="funcionClose" style="margin: 2%;width: 45%;background: #FF5D5E">
                    Calcelar
                </v-btn>
                <v-btn @click="borrarAlarmas()" style="margin: 2%;width: 45%;background: skyblue">
                    Si
                </v-btn>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    import * as firebase from 'firebase';


    export default {
        name: "Alertas",
        components: {
            BarraSuperior
        },
        data() {
            return {
                titulo: "Notificaciones",
                existenAlertas: null,
                tiempo: null,
                lista_alertas: null,
                dataReady:null,
                rules: {
                    required: value => !!value || 'Campo requerido.',
                },
                usuario:
                    {
                        dato1: null,
                        dato2: null,
                        email: null,
                        password: null,
                        apellido: null,
                        nombre: "null",
                        foto: null,
                        tokens: [],
                        IdHogares: [],
                        db: firebase.firestore()
                    }
                ,
                dialogBorrarUna: false,
                dialogBorrarTodas: false,
                dispositivoBorrar: null,
            }
        },
        created: async function(){
            this.dataReady = false;
            let usuarioID = firebase.auth().currentUser.uid;
            let db = firebase.firestore();
            let self = this;
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre)
                self.usuario.email = (doc.data().email)
                self.usuario.apellido = (doc.data().apellido)
                self.usuario.email = (doc.data().email)
                self.usuario.password = (doc.data().password)
                self.usuario.foto = (doc.data().foto)
                self.usuario.tokens = (doc.data().tokens)
                self.usuario.IdHogares = (doc.data().IdHogares)
                console.log("Nombre: " + self.usuario.nombre)
            });

            console.log("UsuarioId = " + usuarioID, self.usuario.email)
            await db.collection("Alarmas").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                self.lista_alertas = [];
                querySnapshot.forEach(function (doc) {
                    let hogar = {
                        id: doc.id,
                        ...doc.data()
                    }
                    let myDate = new Date(hogar.time.seconds * 1000)
                    myDate.toLocaleString("es-ES");
                    hogar.time = myDate.toString()
                    self.lista_alertas.push(hogar)
                    self.existenAlertas = true
                });

                self.lista_alertas.sort(function (a, b) {
                    if (a.timeOrdenar > b.timeOrdenar) {
                        return -1;
                    }
                    if (a.timeOrdenar < b.timeOrdenar) {
                        return 1;
                    }
                    return 0;
                });
            })
            console.log("Alarmas del usuario " + self.usuario.email);
            this.dataReady=true;


        },
        methods: {
            // @vuese
            //Abre el modal de estar seguro borrar una notificación
            botonBorrarAbrirDialog: function (alerta) {
                this.dispositivoBorrar = alerta;
                console.log("alarma " + alerta.apodo)
                this.dialogBorrarUna = true;
            },

            // @vuese
            //Abre el modal de estar seguro borrar todas las notificaciones
            borrarAlarmasAbrirDialog: function () {
                this.dialogBorrarTodas = true
            },

            // @vuese
            //Cierra los modal de borrado
            funcionClose: function () {
                this.dialogBorrarTodas = false;
                this.dialogBorrarUna = false;
            },


            // @vuese
            //Borra una notificacion
            //@arg Notificación que se debe borrar

            botonBorrar: function (alerta) {
                let alertaAuxId = alerta.id;
                console.log("pulsado boton borrar el " + alerta.mensaje + " de id = " + alertaAuxId);
                firebase.firestore().collection("Alarmas").doc(alertaAuxId).delete().then(function () {
                    console.log("Document successfully deleted!");
                    console.log("Document successfully deleted!");
                }).catch(function (error) {
                    console.error("Error removing document: ", error);
                });
                this.dialogBorrarTodas = null;
                this.dialogBorrarUna = null;
            },

            // @vuese
            //Borra todas las notificaciones
            borrarAlarmas: function () {
                let idUser = firebase.auth().currentUser.uid;
                firebase.firestore().collection('Alarmas').where("usuarioAsociado", "==", idUser).get().then(function (querySnapshot) {
                    querySnapshot.forEach(function (doc) {
                        firebase.firestore().collection('Alarmas').doc(doc.data().idAlarma).delete()
                    })
                });
                this.dialogBorrarTodas = false;
                this.dialogBorrarUna = false;
            }

        }
    }
</script>

