                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn style="background: skyblue" color="white " text
                           @click="funcionCloseModificar">Cancelar
                    </v-btn>
                    <v-btn style="background: skyblue" color="white " text
                           :disabled="!datosValidos"
                           @click="funcionGuardarEdicion(sensorDetalles)">Guardar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <!--Dialog borrar-->
        <v-dialog v-model="dialogBorrar" persistent max-width="500px">
            <v-card>
                <v-card-title class="text-center justify-center" style="background: lightskyblue">
                    ¿Estás seguro de borrar <br>{{dispositivoBorrarApodo}}?
                </v-card-title>
                <v-btn style="margin: 2%;width: 45%;background: #FF5D5E" @click="funcionClose">
                    Calcelar
                </v-btn>
                <v-btn style="margin: 2%;width: 45%;background: skyblue" @click="botonBorrar(dispositivoBorrar)">
                    Si
                </v-btn>
            </v-card>
        </v-dialog>

        <v-dialog style="margin: 2px" class="text-center justify-center" v-model="dialogInfo" max-width="500px">
            <v-card-title class="text-center justify-center" style="background: lightskyblue;margin-bottom: 2px">
                Rangos de polución
            </v-card-title>
            <v-img src="/img/icons/ValoresPolucion.png"
            ></v-img>
        </v-dialog>

        <v-dialog v-model="dialogSilenciarTemperatura" max-width="500px">
            <v-card class="text-center justify-center">
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de temperatura
                        han sido silenciadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogSonorizarTemperatura" max-width="500px">
            <v-card class="text-center justify-center">
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de temperatura
                        han sido
                        activadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogSilenciarHumedad" max-width="500px">
            <v-card class="text-center justify-center">
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de humedad
                        han sido
                        silenciadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogSonorizarHumedad" max-width="500px">
            <v-card class="text-center justify-center">
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de humedad
                        han sido
                        activadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogSilenciarPolucion" max-width="500px">
            <v-card class="text-center justify-center">
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de polución
                        han sido
                        silenciadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-dialog v-model="dialogSonorizarPolucion" max-width="500px">
            <v-card>
                <v-card-text class="text-center justify-center">
                    <p><b>Las notificaciones de polución
                        han sido
                        activadas
                        en el sensor {{dispositivoEditado}}</b></p>
                </v-card-text>
            </v-card>
        </v-dialog>


    </v-container>
</template>

<script>
    import * as firebase from 'firebase';
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    import axios from 'axios'


    export default {
        name: 'Dispositivos',
        components: {
            BarraSuperior,
        },


        data() {
            return {
                //Toolbar
                grupos_dispositivos: [],
                hogar_mostrar: "Dispositivos de todos los hogares",
                //Toolbar
                dataReady: false,
                dialogInfo: false,
                sliderData: {
                    min: 0,
                    max: 150,
                    slider: 40,
                    range: [30, 70]
                },
                sliderDataHumedad: {
                    min: 0,
                    max: 100,
                    slider: 40,
                    range: [20, 80]
                },

                dialogSilenciarTemperatura: false,
                dialogSilenciarHumedad: false,
                dialogSilenciarPolucion: false,
                dialogSonorizarTemperatura: false,
                dialogSonorizarHumedad: false,
                dialogSonorizarPolucion: false,
                dispositivoEditado: null,

                combinacionTemperatura: false,
                mostrar_hogar: null,
                mostrar_hogarAux: null,
                auxMostrar: null,
                combinacionHumedad: false,
                idSensorDetalles: null,
                dialogModificar: false,
                dialogBorrar: false,
                existeHogar: false,
                sensorDetalles: null,
                datosValidos: false,
                titulo: "Dispositivos",
                radioGroup: 1,
                row: "C",
                drawer: true,
                dialog: false,
                disabled: false,
                disabledHumedad: false,
                disabledEditar: false,
                disabledPolucion: false,
                dispositivoBorrarId: null,
                dispositivoBorrarApodo: null,
                dispositivoBorrarHogar: null,
                dispositivoBorrar: null,
                min: 0,
                max: 80,
                minHumedad: 0,
                maxHumedad: 50,
                rules: {
                    required: value => !!value || 'Campo requerido.',
                    minId: v => v.length >= 4 || 'EL ID tiene que ser de 5 digitos',
                    valorMax: v => v > this.min || 'EL valor máximo no es válido',
                    valorMin: v => v < this.max || 'EL valor mínimo no es válido',
                    temperaturaValida: () => this.combinacionTemperatura == true || 'Los valores han de ser validos',
                    humedadValida: () => this.combinacionHumedad == true || 'Los valores han de ser validos'
                },
                home: {
                    title: 'Home',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHome'
                },
                hogares: {
                    title: 'Hogares',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHogares'
                },
                dispositivos: {
                    title: 'Dispositivos',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionDispositivos'
                },
                alertas: {
                    title: 'Alertas',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionAlertas'
                },

                slider: 40,
                range: [0, 80],
                hogar: null,
                dispositivo: {
                    identificacion: null,
                    apodo: null
                },
                informacion: true,


                mini: true,
                minUsu: null,
                maxUsu: null,

                lista_dispositivos: null,
                listaAux: [],
                hogaresRegistrados: [],
                hogaresRegistradosID: [],
                usuario:
                    {
                        email: null,
                        password: null,
                        apellido: null,
                        nombre: "null",
                        foto: null,
                        db: firebase.firestore()
                    }
                ,
            }

        },
        created: async function () {
            this.dataReady = false;
            let usuarioID = firebase.auth().currentUser.uid;
            let db = firebase.firestore();
            let self = this;
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre)
                self.usuario.email = (doc.data().email)
                self.usuario.apellido = (doc.data().apellido)
                self.usuario.password = (doc.data().password)
                self.usuario.foto = (doc.data().foto)
            })


            console.log("Usuario: " + usuarioID)
            //Muestro al inicio los sensores
            await db.collection("Dispositivos").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                self.lista_dispositivos = [];//la vacio antes de volver a llenrala con la nueva
                self.listaAux = [];
                querySnapshot.forEach(function (doc) {
                    let dispositivo = {
                        id: doc.id,
                        ...doc.data()
                    }
                    self.lista_dispositivos.push(dispositivo)
                    if (self.hogar_mostrar == dispositivo.hogarAsociado || self.hogar_mostrar == "Dispositivos de todos los hogares") {
                        self.listaAux.push(dispositivo)
                    }
                });
            });
            //cargo en el menu de hogaresRegistrados los registrados
            await db.collection("Hogares").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                querySnapshot.forEach(function (doc) {
                    // doc.data() is never undefined for query doc snapshots
                    let hogar = {
                        id: doc.id,
                        ...doc.data()
                    };
                    self.hogaresRegistrados.push(hogar.apodo);
                    self.grupos_dispositivos.push(hogar.apodo);
                    self.hogaresRegistradosID.push(hogar);
                    self.existeHogar = true
                });
            });
            self.grupos_dispositivos.push("Dispositivos de todos los hogares");
            console.log("Dispositivos del usuario " + self.usuario.email)
            this.dataReady = true;


        },

        watch: {
            // @vuese
            // Guarda los dispositivos filtrados por hogar
            // @arg Apodo del hogar del que se muestra los dispositivos
            hogar_mostrar: function (value) {
                console.log(value)
                if (value != 'Dispositivos de todos los hogares') {
                    console.log("Muetsro los de " + value);
                    this.mostrar_hogarAux = value;
                    let self = this;
                    this.listaAux = [];
                    for (let i = 0; i < this.lista_dispositivos.length; i++) {
                        if (self.lista_dispositivos[i].hogarAsociado == value) {
                            self.listaAux.push(self.lista_dispositivos[i])
                        }
                    }
                } else {
                    console.log("Todos");
                    this.mostrar_hogarAux = value;
                    this.listaAux = [];
                    this.listaAux = this.lista_dispositivos;
                }

            },

            // @vuese
            // Escucha todo el rato que el valor mínimo de temperatura sea menor que el máximo
            min: function (value) {
                if (value < this.max) {
                    this.combinacionTemperatura = true;
                    this.$refs.max.validate();
                } else {
                    this.combinacionTemperatura = false;
                }
            },

            // @vuese
            // Escucha todo el rato que el valor máximo de temperatura sea mayor que el mínimo
            max: function (value) {
                if (value > this.min) {
                    this.combinacionTemperatura = true;
                    this.$refs.min.validate();
                } else {
                    this.combinacionTemperatura = false;
                }
            },

            // @vuese
            // Escucha todo el rato que el valor mínimo de humedad sea menor que el máximo
            minHumedad: function (value) {
                if (value < this.maxHumedad) {
                    this.combinacionHumedad = true;
                    this.$ref.maxHumedad.validate();
                } else {
                    this.combinacionHumedad = false;
                }
            },

            // @vuese
            // Escucha todo el rato que el valor máximo de humedad sea mayor que el mínimo
            maxHumedad: function (value) {
                if (value > this.minHumedad) {
                    this.combinacionHumedad = true;
                    this.$ref.minHumedad().validate();
                } else {
                    this.combinacionHumedad = false;
                }
            }

        },


        methods: {

            // @vuese
            //Guarda los datos del sensor editado
            //@arg Sensor el cual es editado
            funcionGuardarEdicion: async function (sensor) {
                console.log("pulsado guardar edicion del sensor " + sensor.identificacion + sensor.apodo);
                this.dialogModificar = false
                console.log("Nuevos valores: " + this.disabledEditar + this.disabledHumedad + this.disabledPolucion + this.min + this.max)
                console.log("hay que actualizar");

                //Hay que ver si hay valores peligrosos en los sensores. Ej:
                //Si actaulizo desde la modificacion como ultima noti, y polucion no era epligrosa entonces pero si 5 minutos depeuss,
                //tendria que esperar otros 5 minutos, y tendria que llegar en el momento
                var ultimaH = null;
                var ultimaT = null;
                var ultimaP = null;

                if (parseFloat(this.sliderData.range[1]) < parseFloat(sensor.tempActual)) {//Si es mayor, esa ya se cuenta como ultima actualizacion y se coge la hora
                    ultimaT = new Date().getTime();
                    console.log("el sensor " + sensor.apodo + " SI coge modificar como ultima en temepratura Max")
                } else if (parseFloat(this.sliderData.range[0]) > parseFloat(sensor.tempActual)) {//Si es menor, esa ya se cuenta como ultima actualizacion y se coge la hora
                    ultimaT = new Date().getTime();
                    console.log("el sensor " + sensor.apodo + " SI coge modificar como ultima en temepratura Min")
                } else {
                    ultimaT = 0;//Si es menor, no se coge como actualizacion y se pone a 0 para asi se notifique cuando queire
                    console.log("el sensor " + sensor.apodo + " NO coge modificar como ultima en temepratura")
                }

                if (parseFloat(this.sliderDataHumedad.range[1]) < parseFloat(sensor.humedadActual)) {//Si es mayor, esa ya se cuenta como ultima actualizacion y se coge la hora
                    ultimaH = new Date().getTime();
                    console.log("el sensor " + sensor.apodo + " SI coge modificar como ultima en humedad Max")
                } else if (parseFloat(this.sliderDataHumedad.range[0]) > parseFloat(sensor.humedadActual)) {//Si es menor, esa ya se cuenta como ultima actualizacion y se coge la hora
                    ultimaH = new Date().getTime();
                    console.log("el sensor " + sensor.apodo + " SI coge modificar como ultima en humedad Min")
                } else {
                    ultimaH = 0;//Si es menor, no se coge como actualizacion y se pone a 0 para asi se notifique cuando queire
                    console.log("el sensor " + sensor.apodo + " NO coge modificar como ultima en humedad")
                }

                if (100 < parseFloat(sensor.polucionActual)) {//Si es mayor, esa ya se cuenta como ultima actualizacion y se coge la hora
                    ultimaP = new Date().getTime();
                    console.log("el sensor " + sensor.apodo + " SI coge modificar como ultima en polucion")
                } else {
                    ultimaP = 0;//Si es menor, no se coge como actualizacion y se pone a 0 para asi se notifique cuando queire
                    console.log("el sensor " + sensor.apodo + " NO coge modificar como ultima en polucion")
                }

                await firebase.firestore().collection('Dispositivos').doc(sensor.identificacion).set({
                    //Supongamos que, al modificar, se peueda en si o se lance notificacion al modificar
                    //Y a oartir de ahi, cada 10 minutos
                    formato: this.row,
                    ultimaNotiH: ultimaH,
                    ultimaNotiT: ultimaT,
                    ultimaNotiP: ultimaP,
                    poderNotiH: true,//permito poderNotiH al modificar, asi llega la notificacion
                    poderNotiP: true,
                    poderNotiT: true,
                    tempMax: this.sliderData.range[1],
                    tempMin: this.sliderData.range[0],
                    humedadMax: this.sliderDataHumedad.range[1],
                    humedadMin: this.sliderDataHumedad.range[0],
                    disabledHumedad: this.disabledHumedad,
                    disabledTemperatura: this.disabledEditar,
                    disabledPolucion: this.disabledPolucion,
                }, {merge: true})
                this.max = this.min = 0;
                this.dispositivo.identificacion = this.dispositivo.identificacion = this.hogar = null;
                this.disabled = this.disabledTemperatura = this.disabledHumedad = this.disabledPolucion = false;
            },



            // @vuese
            // Cierra el modal de crear dispositivo
            funcionClose: function () {
                this.dialog = false;
                this.dialogBorrar = false;
                this.dispositivo.identificacion = this.dispositivo.apodo = this.hogar = null;
                this.disabled = this.disabledTemperatura = this.disabledHumedad = this.disabledPolucion = false;
                this.dispositivo.identificacion = null;
                this.dispositivo.apodo = null;
                this.hogar = null;
                this.disabled = false;
                this.disabledHumedad = false;
                this.disabledPolucion = false;
                this.sliderData.range[0] = 30;
                this.sliderData.range[1] = 70;
                this.sliderDataHumedad.range[0] = 20;
                this.sliderDataHumedad.range[1] = 80;

            },

            // @vuese
            // Cierra el modal de guardar la edición de un dispositivo
            funcionCloseModificar: function () {
                console.log("close")
                this.dialogModificar = false;
                this.dispositivo.identificacion = this.dispositivo.identificacion = this.hogar = null;
                this.disabled = this.disabledTemperatura = this.disabledHumedad = this.disabledPolucion = false;
            },


            // @vuese
            // Abre el modal de modificar un dispositivo
            openModificar: function (sensor) {
                this.dialogModificar = true;
                this.sensorDetalles = sensor;

            },


            //Añado el sensor. Supingogamos que cada sensor tiene un ID unico, que usaremos como identificador ne la coleccion
            //ademas, asi me sera mucho mas facil editarlo
            // @vuese
            // Guarda los datos de un nuevo sensor creado
            funcionSave: async function () {
                console.log("save")
                this.dialog = false;
                let db = firebase.firestore();
                let vectorSensoresAux = {}
                let idSensor = this.dispositivo.identificacion
                var info = null
                var infoHumedad = null
                var infoPolucion = null


                //De primeras esta bien coger una primera vez los datos de temperatura actual
                let URL = `https://www.purpleair.com/json?show=` + this.dispositivo.identificacion;
                axios.get(URL)
                    .then(async (response) => {
                        if (response.data.results[0] != undefined) {
                            info = (response.data.results[0].temp_f.toString())
                            infoHumedad = (response.data.results[0].humidity.toString())
                            infoPolucion = (response.data.results[0].p_2_5_um.toString())
                        }
                        if (info == null) {
                            alert('El ID del sensor no está registrado')
                        } else {
                            console.log("paso");
                            console.log("Hogar--> '" + this.hogar + "' IdSensor --> " + idSensor);
                            //Primero se añade al hogar al que correcponde en su vector
                            await db.collection("Hogares").where("apodo", "==", this.hogar).where("usuarioAsociado", "==", firebase.auth().currentUser.uid).get().then((querySnapshot) => {
                                querySnapshot.forEach((doc) => {
                                    // doc.data() is never undefined for query doc snapshots
                                    let idHogar = doc.data().idHogar
                                    vectorSensoresAux = doc.data().vectorSensores;
                                    vectorSensoresAux.push(idSensor)
                                    console.log(doc.data().apodo + " 0:" + idHogar)
                                    console.log(doc.data().apodo + " 1:" + vectorSensoresAux)

                                    db.collection("Hogares").doc(idHogar).set({
                                        vectorSensores: vectorSensoresAux
                                    }, {merge: true})
                                });
                            });

                            //Se guarda diferente dependiendo si prefiere grados Farenheint o Celsius

                            if (this.row == 'F') {
                                await firebase.firestore().collection("Dispositivos").doc(this.dispositivo.identificacion).set({
                                    identificacion: this.dispositivo.identificacion,
                                    apodo: this.dispositivo.apodo,
                                    usuarioAsociado: firebase.auth().currentUser.uid,
                                    hogarAsociado: this.hogar,
                                    activadaTemperatura: this.disabled,
                                    formato: this.row,//C= celsius, F = fharheint
                                    tempMax: this.sliderData.range[1],
                                    tempMin: this.sliderData.range[0],
                                    //tempMax: this.max,
                                    //tempMin: this.min,
                                    tempActual: null,
                                }, {merge: true});
                            } else {
                                await firebase.firestore().collection("Dispositivos").doc(this.dispositivo.identificacion).set({
                                    identificacion: this.dispositivo.identificacion,
                                    apodo: this.dispositivo.apodo,
                                    activadaTemperatura: this.disabled,
                                    usuarioAsociado: firebase.auth().currentUser.uid,
                                    hogarAsociado: this.hogar,
                                    formato: this.row,//C= celsius, F = fharheint
                                    tempActual: null,
                                    tempMax: this.sliderData.range[1],
                                    tempMin: this.sliderData.range[0],
                                    //tempMax: this.max,
                                    //tempMin: this.min,
                                }, {merge: true});
                            }
                            //Añado aqui humedad proque no hay opciones de diferentes medidas, al igual que en la polución
                            await firebase.firestore().collection('Dispositivos').doc(this.dispositivo.identificacion).set({
                                tempActual: info,
                                humedadActual: infoHumedad,
                                polucionActual: infoPolucion,
                                //humedadMax: this.maxHumedad,
                                //humedadMin: this.minHumedad,
                                estado: true,
                                humedadMax: this.sliderDataHumedad.range[1],
                                humedadMin: this.sliderDataHumedad.range[0],
                                disabledHumedad: this.disabledHumedad,
                                activadoSonidoTemperatura: true,
                                activadoSonidoHumedad: true,
                                activadoSonidoPolucion: true,
                                disabledTemperatura: this.disabled,
                                disabledPolucion: this.disabledPolucion,
                                ultimaNotiT: 0,
                                ultimaNotiH: 0,
                                ultimaNotiP: 0,
                                poderNotiT: true,
                                poderNotiH: true,
                                poderNotiP: true,
                            }, {merge: true})
                            //Al crear el dispositivo, tengo que crear la coleccion del Historial
                            await firebase.firestore().collection('Dispositivos').doc(this.dispositivo.identificacion).collection('Historial').add({
                                time: new Date(),
                                humedad: infoHumedad,
                                polucion: infoPolucion,
                                temperatura: info
                            })
                            //pongo los datos de por defecto
                            this.dispositivo.identificacion = null;
                            this.dispositivo.apodo = null;
                            this.hogar = null;
                            this.disabled = false;
                            this.disabledHumedad = false;
                            this.disabledPolucion = false;
                            this.sliderData.range[0] = 30;
                            this.sliderData.range[1] = 70;
                            this.sliderDataHumedad.range[0] = 20;
                            this.sliderDataHumedad.range[1] = 80;

                        }
                        return response
                    })

                    .catch(error => console.log(error))

            },

            // @vuese
            // Muestra los detalles de un dispositivo
            //@arg Dispositivo a mostrar datos
            botonDetalles: function (dispositivo) {
                console.log("pulsado boton detalles " + dispositivo)
                this.idSensorDetalles = dispositivo.identificacion
                const parametro = dispositivo.identificacion
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {

                        this.$router.push({
                            name: 'DetallesDispositivo',
                            params: {idSensor: parametro, apodo: dispositivo.apodo, origen: "Dispositivos"}
                        })
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: ' + err.message)

                        console.log('Opps ' + err.message);
                    }
                );

            },

            // @vuese
            // Borra un dispositivo
            //@arg Dispositio a borrar
            botonBorrar: async function (dispositivo) {
                console.log("pulsado boton borrar el " + dispositivo.identificacion);
                this.dialogBorrar = false;

                //priemro borro en el vector de hogares que esta guardado
                await firebase.firestore().collection("Hogares").where("apodo", "==", dispositivo.hogarAsociado).where("usuarioAsociado", "==", firebase.auth().currentUser.uid).get().then((querySnapshot) => {
                    querySnapshot.forEach(async (doc) => {
                        let vectorSensoresAux = doc.data().vectorSensores;
                        for (let i = 0; i < vectorSensoresAux.length; i++) {
                            if (vectorSensoresAux[i] == dispositivo.identificacion) {
                                vectorSensoresAux.splice(i, 1)
                                console.log("entro " + doc.data().vectorSensores)
                                console.log(vectorSensoresAux)
                                await firebase.firestore().collection("Hogares").doc(doc.id).set({
                                    vectorSensores: vectorSensoresAux
                                }, {merge: true})
                            }
                        }
                    })
                });


                //una vez borrado los campos, se borra el historial
                var batch = firebase.firestore().batch();
                let contador = 0;
                let self = this;
                await firebase.firestore().collection("Dispositivos").doc(dispositivo.identificacion).collection("Historial").get().then(async (querySnapshot) => {
                    querySnapshot.forEach((doc) => {
                        console.log("Borro-----------" + doc.data().time)
                        if (contador == 499) {
                            batch.commit()
                            self.contador = 0
                        }
                        contador++;
                        var laRef = (doc.ref)
                        batch.delete(laRef)

                    });
                    await batch.commit()
                });

                //una vez borrado los campos, se borra ya el documento
                await firebase.firestore().collection("Dispositivos").doc(dispositivo.identificacion).delete().then(function () {
                    console.log("Document successfully deleted!");
                }).catch(function (error) {
                    console.error("Error removing document: ", error);
                });

            },


            // @vuese
            // Cambia la activación de sonido en temperatura en un dispositivo
            // @arg Dispositivo a editar y hogar al que pertenece
            cambioSonidoTemperatuta: async function (dispositivo, hogar) {
                this.dispositivoEditado = dispositivo.apodo;
                dispositivo.activadoSonidoTemperatura = !dispositivo.activadoSonidoTemperatura;
                if (dispositivo.activadoSonidoTemperatura == false) {
                    //alert("Las notificaciones de temperaturas han sido silenciadas en " + dispositivo.apodo)
                    this.dialogSilenciarTemperatura = true;
                } else {
                    //alert("Las notificaciones de temperaturas han sido activadas en " + dispositivo.apodo)
                    this.dialogSonorizarTemperatura = true;
                }
                await firebase.firestore().collection('Dispositivos').doc(dispositivo.identificacion).set({
                    activadoSonidoTemperatura: dispositivo.activadoSonidoTemperatura
                }, {merge: true});
                console.log(hogar)
            },

            // @vuese
            // Cambia la activación de sonido en humedad en un dispositivo
            // @arg Dispositivo a editar y hogar al que pertenece
            cambioSonidoHumedad: async function (dispositivo, hogar) {
                this.dispositivoEditado = dispositivo.apodo;
                dispositivo.activadoSonidoHumedad = !dispositivo.activadoSonidoHumedad;
                if (dispositivo.activadoSonidoHumedad == false) {
                    this.dialogSilenciarHumedad = true;
                } else {
                    this.dialogSonorizarHumedad = true;
                }
                await firebase.firestore().collection('Dispositivos').doc(dispositivo.identificacion).set({
                    activadoSonidoHumedad: dispositivo.activadoSonidoHumedad
                }, {merge: true})
                console.log(hogar)
            },

            // @vuese
            // Cambia la activación de sonido en polución en un dispositivo
            // @arg Dispositivo a editar y hogar al que pertenece
            cambioSonidoPolucion: async function (dispositivo, hogar) {
                this.dispositivoEditado = dispositivo.apodo;
                dispositivo.activadoSonidoPolucion = !dispositivo.activadoSonidoPolucion;
                if (dispositivo.activadoSonidoPolucion == false) {
                    this.dialogSilenciarPolucion = true
                } else {
                    this.dialogSonorizarPolucion = true
                }
                await firebase.firestore().collection('Dispositivos').doc(dispositivo.identificacion).set({
                    activadoSonidoPolucion: dispositivo.activadoSonidoPolucion
                }, {merge: true});
                console.log(hogar)
            },


            // @vuese
            // Abre el modal de borrar un dispositivo
            abrirDialogBorrar: function (dispositivo) {
                this.dispositivoBorrarId = dispositivo.identificacion;
                this.dispositivoBorrarApodo = dispositivo.apodo;
                this.dispositivoBorrarHogar = dispositivo.hogarAsociado;
                this.dispositivoBorrar = dispositivo;
                console.log("dispositivo = " + dispositivo);
                this.dialogBorrar = true;
            }

        },
    }

</script>

