<template>
    <v-container class="EditarPerfil ma-0 pa-0" >
        <barra-superior :titulo="titulo"></barra-superior>
        <v-row no-gutters justify="space-around">
        <v-row v-if="datosListos" justify="space-around">
                <v-col style="align: center; width: auto; margin-top: 60px" >
                    <div>

                        <h3 >Información personal</h3>
                        <div >
                            Nombre: {{usuario.nombre}}<br>
                            Apellido: {{usuario.apellido}}<br>
                            Correo electrónico: {{usuario.email}}<br>

                        </div>
                    </div>
                    <v-btn  @click="funcionCambiarDatos" rounded  style=" background: skyblue; margin: 10px; width: auto" >Editar información</v-btn>
                    <br>

                    <v-app>
                        <v-row justify="center">
                            <v-dialog v-model="dialog" persistent max-width="290">
                                <template v-slot:activator="{ on }">
                                    <v-btn dark v-on="on" rounded  style="  background: #FF5D5E; margin: 10px; width: auto" >Borrar cuenta</v-btn>
                                </template>
                                <v-card>
                                    <v-card-text> ¿Estás seguro de borrar la cuenta y todos los datos relaccionados?</v-card-text>
                                    <v-card-actions>
                                        <v-spacer></v-spacer>
                                        <v-btn style="margin: 2%;width: 45%;background: #FF5D5E" text @click="dialog = false">Cancelar</v-btn>
                                        <v-btn style="margin: 2%;width: 45%;background: skyblue" text @click="funcionBorrarDatos">Si</v-btn>
                                    </v-card-actions>
                                </v-card>
                            </v-dialog>
                        </v-row>
                    </v-app>
                </v-col>

        </v-row>
        </v-row>





    </v-container>
    
</template>

<script>
    import * as firebase from 'firebase';
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    export default {
        name: "EditarPerfil",
        components: {
            BarraSuperior
        },
        data(){
            return{
                titulo: "PERFIL",
                datosListos: false,
                dialog: false,
                drawer: true,
                home:{
                    title: 'Home',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHome'
                },
                hogares:{
                    title: 'Hogares',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionHogares'
                },
                dispositivos:{
                    title: 'Dispositivos',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionDispositivos'
                },
                alertas:{
                    title: 'Alertas',
                    icon: 'mdi-home-city' ,
                    clickFuncion: 'funcionAlertas'
                },



                mini: true,
                usuario:
                    {
                        email: null,
                        password : null,
                        apellido: null,
                        nombre:"null",
                        foto:null,
                        db: firebase.firestore()},
            }
        },
        created:function() {
            let db = firebase.firestore();
            db.collection("Usuarios").doc(firebase.auth().currentUser.uid).get().then( (doc) =>{
                this.usuario.nombre=(doc.data().nombre)
                this.usuario.email=(doc.data().email)
                this.usuario.apellido=(doc.data().apellido)
                this.usuario.email=(doc.data().email)
                this.usuario.password=(doc.data().password)
                this.usuario.foto=(doc.data().foto)
                console.log(this.usuario.foto)

            }).catch(function(error) {
                console.log("Error getting document:", error);
            });
            this.datosListos = true;
        },
        methods:{

            // @vuese
            //Redirecciona a la entana de editar datos
            funcionCambiarDatos: function(){
                console.log("funcion ir editar informacion")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        this.$router.replace('EditarInformacion')
                        console.log(user)
                    },
                    function (err) {
                        console.log('Opps '+err.message);
                    }
                );
            },


            // @vuese
            // Permite borrar una cuenta, incluyendo todos sus datos relaccionados
            funcionBorrarDatos:async function(){
                this.dialog=false;
                let userID = await firebase.auth().currentUser.uid;
                console.log("funcion borrar usuario "+userID);

                //Primero borro las alarmas del usuario asociado
                firebase.firestore().collection('Alarmas').where("usuarioAsociado","==",userID).get().then(function (querySnapshot) {
                    querySnapshot.forEach(function (doc) {
                        firebase.firestore().collection('Alarmas').doc(doc.data().idAlarma).delete()
                        console.log("Borrada notificacion")
                    })
                });

                //Despues borro los dispositivos, con su respectivo historial


                console.log("Paso al dipositivos")
                await firebase.firestore().collection('Dispositivos').where("usuarioAsociado","==",userID).get().then(function (querySnapshot) {
                    querySnapshot.forEach(function (doc) {

                        //Primero hisotorial
                        var batch = firebase.firestore().batch();
                        let contador = 0;
                        let self = this;
                        firebase.firestore().collection("Dispositivos").doc(doc.data().identificacion).collection("Historial").get().then((querySnapshot) => {
                            querySnapshot.forEach((doc) => {
                                console.log("Borro-----------"+ doc.data().time)
                                if (contador == 499) {
                                    batch.commit()
                                    self.contador = 0
                                }
                                contador++;
                                var laRef = (doc.ref)
                                batch.delete(laRef)
                            });
                            batch.commit()
                        });

                        //SEgundo el documento en si
                        firebase.firestore().collection('Dispositivos').doc(doc.data().identificacion).delete();
                        console.log("Borrada dispositivo")
                    })
                });

                console.log("Paso al hogares")
                //Despues borro los hogares
                await firebase.firestore().collection('Hogares').where("usuarioAsociado","==",userID).get().then(function (querySnapshot) {
                    querySnapshot.forEach(function (doc) {
                        firebase.firestore().collection('Hogares').doc(doc.data().idHogar).delete()
                        console.log("Borrada hogar")
                    })
                });



                //Ahora toca borrarlo d los usuarios (en la base de datos)
                await firebase.firestore().collection('Usuarios').doc(userID).delete().then(function () {
                    console.log("Usuario borrado")
                }).catch(function(error) {
                    console.error("Error removing document: ", error);
                });


               //por ultimo queda  deregistrar de firestore y sus users
                await firebase.auth().currentUser.delete().then(function () {
                    console.log("Usuario deregistrado")
                }).catch(function(error) {
                    console.error(error)
                });

                this.$router.replace('login');

            },


        },
    }

</script>

<style scoped>

</style>
