<template>
    <v-container class="Hogares ma-0 pa-0" fluid>
        <barra-superior :titulo="titulo"></barra-superior>
        <v-row  no-gutters justify="space-around" v-if="this.dataReady===true" >
            <v-col >
                <div class="gb_Qe" style="visibility: hidden; left: 8px; top: 56px; "></div>
                <v-col  style="margin-bottom: 80px">
                    <v-card-title class="headline justify-center">HOGARES</v-card-title>
                    <h4 style="margin-top: 10%; background: aquamarine" v-if="this.lista_hogares.length == 0 && this.dataReady === true">
                        <div v-if="this.dataReady === true">
                            No hay hogares registrados aún<br>
                            <h4 style="font-weight: bold">Registra un hogar para poder asociar un sensor</h4>
                        </div>
                    </h4>
                    <v-container   style="display: inline-block;width: 100%; max-width: 400px" v-for="hogar in lista_hogares" :key="hogar.id">
                        <v-row dense style="width: 100%;">
                            <v-card raised style="width: 100%; text-align: left;">
                                <v-card-title style="font-weight: bold " class="headline">{{hogar.apodo}}</v-card-title>
                                <v-card-subtitle v-if="hogar.direccion != null">{{hogar.direccion}}</v-card-subtitle>
                                <v-card-subtitle style="min-height: 38px" v-else></v-card-subtitle>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn dark style="background: skyblue; margin: 3px; width: 34% " text
                                           @click="abrirDialogModificar(hogar)">
                                        Modificar
                                    </v-btn>
                                    <!--mete un hueco todo lo grande podible en el hueco que este-->
                                    <v-btn dark style="background: skyblue;margin: 3px;width: 31%" text
                                           @click="abrirDialogDetalles(hogar)">Detalles
                                    </v-btn>

                                    <v-btn dark style="background: #FF5D5E ;margin: 3px;width: 30%" text
                                           @click="abrirDialogBorrar(hogar)">Borrar
                                    </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-row>
                    </v-container>


                        <v-btn fixed fab right bottom color="primary" dark @click="abrirDialogCrear(null)">
                            <v-icon>mdi-plus</v-icon>
                        </v-btn>
                    <v-dialog max-width="600px" v-model="dialogCrear" persistent>
                        <v-card>

                            <v-card-title class="white--text" style="background: #05c5ff" v-if="opcion == null">
                                <span class="headline">Nuevo Hogar </span>
                            </v-card-title>

                            <v-card-title class="text-center justify-center white--text" style="background: #05c5ff" v-else>
                                <span class="headline">Modificar {{opcion.apodo}}</span>
                            </v-card-title>

                            <v-card-text>
                                <v-container>
                                    <v-form v-model="datosValidos2">
                                        <v-row>
                                            <v-divider></v-divider>
                                            <!--primero el alias del hogar -->
                                            <v-col cols="12" v-if="opcion == null">
                                                <v-text-field v-model="hogar.apodo"
                                                              label="Apodo o alias"
                                                              hint="Ej: 'Casa Abuelo' o 'Casa Playa'"
                                                              :rules="[rules.required]"

                                                ></v-text-field>
                                            </v-col>
                                            <v-col cols="12"  v-else>
                                                <v-text-field v-model="hogarModificarApodo"
                                                              label="Apodo"
                                                              hint="Ej: 'Casa Abuelo' o 'Casa Playa'"
                                                              :rules="[rules.required]"
                                                ></v-text-field>
                                            </v-col>


                                            <v-divider></v-divider>

                                            <!--segundo la direccion del hogar
                                             Como solo se peude cargar en un tetxfiel,
                                             guardo los datos en el mismo, y segun loq ue me pasar al principio,
                                              defido guardalo de una forma u otra mostrando o un boton de guardado
                                              u otro.
                                              El autocomplete no cambia para nada, es autocomplete
                                             -->
                                            <v-col cols="12">
                                                <!--<GoogleMap/>-->
                                                <div>
                                                    <div>
                                                        <v-text-field ref="autocomplete"
                                                                      :rules="[rules.required]"
                                                                      type="text"/>
                                                        <br/>
                                                    </div>
                                                    <br>
                                                    <gmap-map :center="center" :zoom="12" style="width:100%;  height: 200px;">
                                                        <gmap-marker
                                                                :key="index"
                                                                v-for="(m, index) in markers"
                                                                :position="m.position"
                                                                @click="center=m.position"
                                                        ></gmap-marker>
                                                    </gmap-map>
                                                </div>
                                            </v-col>
                                        </v-row>
                                    </v-form>
                                </v-container>
                            </v-card-text>
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn style="color: white; background: deepskyblue" text @click="funcionClose">Cerrar
                                </v-btn>

                                <!--elijo un boton u otro dependiendo quetenga opcion
                                opcion = null => hayq ue crear
                                opcion != null => hay que modificar
                                -->
                                <v-btn v-if="opcion == null" :disabled="!datosValidos2" style="color: white; background: deepskyblue" text
                                       @click="funcionSave">Guardar
                                </v-btn>
                                <v-btn v-else :disabled="!datosValidos2" style="color: white; background: deepskyblue" text
                                       @click="funcionGuardarEdicion">Guardar
                                </v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>

                </v-col>
            </v-col>
        </v-row>
        <v-row v-else>
            <v-layout align-center justify-center style="margin-top: 60px">
                <v-progress-circular
                        indeterminate
                        color="blue"
                        size="250"
                        class="mb-0"
                ></v-progress-circular>
            </v-layout>
        </v-row>

        <!--dialog modificar-->
        <v-dialog v-model="dialogModificar" persistent max-width="600px">
            <v-card>
                <v-card-title style="background: aqua">
                    <span class="headline">Modificar {{hogarModificarApodo}}</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-form v-model="datosValidos">
                            <v-row>
                                <v-divider></v-divider>
                                <v-col cols="12">
                                    <v-text-field v-model="hogarModificarApodo"
                                                  label="Apodo"
                                                  hint="Ej: 'Casa Abuelo' o 'Casa Playa'"
                                                  :rules="[rules.required]"
                                    ></v-text-field>
                                </v-col>
                                <v-divider></v-divider>
                                <v-col cols="12">
                                    <!--<GoogleMap/>-->


                                </v-col>
                            </v-row>
                        </v-form>
                    </v-container>
                </v-card-text>


                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn style="color: white; background: deepskyblue" text
                           @click="funcionClose">Cancelar
                    </v-btn>
                    <v-btn style="color: white; background: deepskyblue" text
                           :disabled="!datosValidos"
                           @click="funcionGuardarEdicion">Guardar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <!--Dialog detalles-->
        <v-dialog v-model="dialogDetalles" persistent max-width="500px" >
            <v-card>
                <div v-if="hogarDetalles != null" >
                    <v-card-title style="background: skyblue" class="text-center justify-center" >
                        <span class="headline">Sensores asociados en <br> {{hogarDetalles.apodo}}</span>
                    </v-card-title>
                </div>

                <div v-for="sensor in lista_sensores_hogar" :key="sensor.id">
                    <v-card

                            style="margin: 10px;"
                    >
                        <v-card-title  @click="botonDetallesSensor(sensor)" class="headline">
                            {{sensor.apodo}} (Id: {{sensor.identificacion}})
                        </v-card-title>

                    </v-card>
                </div>
                <div v-if="lista_sensores_hogar.length > 0">
                </div>
                <div v-else>
                    No existen sensores asociados al hogar actualmente
                </div>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn style="color: white; background: #FF5D5E;" text
                           @click="funcionClose">Cerrar
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <!--Dialog borrar-->
        <v-dialog v-model="dialogBorrar" persistent max-width="500px" class="text-center justify-center">
            <v-card>
                <v-card-title class="text-center justify-center" style="background: lightskyblue">
                    ¿Estás seguro de borrar <br>{{hogarBorrarApodo}}?
                </v-card-title>
                <v-btn @click="funcionClose" style="margin: 2%;width: 45%;background: #FF5D5E">
                    Calcelar
                </v-btn>
                <v-btn @click="botonBorrar(hogarBorrar)" style="margin: 2%;width: 45%;background: skyblue">
                    Si
                </v-btn>
            </v-card>
        </v-dialog>

    </v-container>
</template>

<script>
    import * as firebase from 'firebase';
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    import {gmapApi} from 'vue2-google-maps';
    export default {
        name: 'Hogares',
        components: {
            BarraSuperior,
        },
        dialog: false,
        data() {
            return {
                //Maps
                center: {lat: 40.4297218, lng: -3.67975},
                markers: [],
                places: [],
                currentPlace: '',
                dataReady:false,
                prueba: '',
                listenerIniciado: false,
                listenerIniciado2: false,
                opcion : null,
                //Maps
                hogarModificar: null,
                hogarModificarDireccion: null,
                hogarModificarApodo:null,
                hogarBorrar:null,
                hogarBorrarDireccion:null,
                hogarBorrarApodo:null,
                hogarApodoAntiguo:null,
                hogarDetalles:null,
                titulo: "Hogares",
                datosValidos: false,
                datosValidos2: false,
                rules: {
                    required: value => !!value || 'Campo requerido.',
                    min: v => (v != null && v.length >= 9) || 'Numero invalido',
                    numero: value => {
                        const pattern = /^(\(?\+?[0-9]*\)?)?[0-9_\- ()]*$/
                        return pattern.test(value) || 'Numero invalido'
                    },
                },
                lista_sensores_hogar: [],
                drawer: true,
                dialog: false,
                dialogModificar: false,
                dialogDetalles: false,
                dialogCrear: false,
                dialogBorrar:false,
                home: {
                    title: 'Home',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHome'
                },
                hogares: {
                    title: 'Hogares',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionHogares'
                },
                dispositivos: {
                    title: 'Dispositivos',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionDispositivos'
                },
                alertas: {
                    title: 'Alertas',
                    icon: 'mdi-home-city',
                    clickFuncion: 'funcionAlertas'
                },

                mini: true,
                hogar: {
                    direccion: null,
                    apodo: null,
                    usuarioAsociado: null,
                    db: firebase.firestore()
                },
                hogarEditar: {
                    direccion: null,
                    apodo: null,
                },
                lista_hogares: null,
                usuario:
                    {
                        dato1: null,
                        dato2: null,
                        email: null,
                        password: null,
                        apellido: null,
                        nombre: "null",
                        foto: null,
                        tokens: [],
                        IdHogares: [],
                        db: firebase.firestore()
                    }
                ,
            }
        },
        computed:{
            // @vuese
            // Carga los datos y mapa de la base de datos de GoogleMaps
            google: gmapApi,
        },
        watch:{

            // @vuese
            //Escucha los cambios en el cuadro de texto proporcionado por GoogleMaps
            google: function () {
                let self = this;
                if(this.google != null){
                    console.log("No null")
                }else{
                    console.log("Null")
                }
                console.log(this.$refs)
                if (this.google != null && this.$refs && this.$refs) {
                    this.autocomplete = new this.google.maps.places.Autocomplete(
                        (this.$refs.autocomplete.$refs.input),
                        {types: ['geocode']}
                    );
                    if(!this.listenerIniciado){
                        self.autocomplete.addListener('place_changed', () => {
                            let place = self.autocomplete.getPlace();
                            self.setPlace(place);
                        });
                        this.listenerIniciado = true;
                    }
                }
            }
        },

        mounted() {
            this.geolocate();
        },

        created: async function () {
            this.dataReady=false;
            let usuarioID = firebase.auth().currentUser.uid;
            let db = firebase.firestore();
            let self = this;
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre)
                self.usuario.email = (doc.data().email)
                self.usuario.apellido = (doc.data().apellido)
                self.usuario.email = (doc.data().email)
                self.usuario.password = (doc.data().password)
                self.usuario.foto = (doc.data().foto)
                self.usuario.tokens = (doc.data().tokens)
                self.usuario.IdHogares = (doc.data().IdHogares)
                console.log("Nombre: " + self.usuario.nombre)
            });
            var format = require('date-format');
            var date = format('SSS', new Date());
            console.log(date)
            //Mostar los hogares al inicio
            await db.collection("Hogares").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                self.lista_hogares = [];//la vacio antes de volver a llenrala con la nueva
                querySnapshot.forEach(function (doc) {
                    let hogar = {
                        id: doc.id,
                        ...doc.data()
                    }
                    self.lista_hogares.push(hogar)
                });
            })
            this.dataReady = true;
        },
        methods: {
            // @vuese
            // Guarda en la variable currentPlace el lugar que se muetra en el mapa
            //@arg Lugar representado en el mapa
            setPlace(place) {
                this.currentPlace = place;
                this.addMarker()
            },

            // @vuese
            //Añade el nombre completo de la direccion mostradad en el mapa
            addMarker() {
                if (this.currentPlace) {
                    this.places = [];
                    const marker = {
                        lat: this.currentPlace.geometry.location.lat(),
                        lng: this.currentPlace.geometry.location.lng()
                    };
                    this.markers.push({position: marker});
                    this.places.push(this.currentPlace);
                    this.center = marker;
                    this.currentPlace = null;
                    console.log(this.places[0].formatted_address)
                }
                this.hogar.direccion = this.places[0].formatted_address
                console.log("direccion tiene: "+this.hogar.direccion)
            },

            // @vuese
            // Inicia el mapa que se genera al crear y modificar un hogar
            geolocate: function () {
                navigator.geolocation.getCurrentPosition(position => {
                    this.center = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    };
                });
            },

            // @vuese
            // Abre un modal que permite editar un hogar
            //@arg Hogar a editar
            abrirDialogModificar: function (hogar) {
                this.opcion = hogar;
                console.log("Abrir dialog modificar "+hogar);
                this.hogarModificar = hogar;
                this.hogarApodoAntiguo = hogar.apodo;//Guarda el apodo antiguo antes de modificar
                this.hogarModificarDireccion = hogar.direccion;
                this.hogarModificarApodo = hogar.apodo;
                this.dialogCrear = true;
                console.log(hogar.apodo)
            },

            // @vuese
            //Abre un modal para crear un hogar
            //@arg Parametro que solo se usa cuando se modifica un hogar para saber cual se modifica
            abrirDialogCrear: function(hogar){
                this.opcion = hogar;
                console.log("Abrir dialog crear "+hogar);
                this.hogarModificar = hogar;
                this.hogarModificarDireccion = hogar;
                this.hogarModificarApodo = hogar;
                this.dialogCrear = true;
                console.log(hogar)
            },

            // @vuese
            // Abre un modal donde muestra los detalles asociados al hogar seleccionado
            //@arg Hogar a examinar
            abrirDialogDetalles: async function(hogar){
                this.hogarDetalles = hogar;
                let self = this
                self.lista_sensores_hogar = []
                console.log("hola 1 -->" + this.lista_sensores_hogar)
                for (let i = 0; i < self.hogarDetalles.vectorSensores.length; i++) {
                     await firebase.firestore().collection('Dispositivos').doc(self.hogarDetalles.vectorSensores[i]).get().then(function (doc) {
                        console.log(doc.data())
                        self.lista_sensores_hogar.push(doc.data())
                        console.log("hola 2 -->" + self.lista_sensores_hogar)
                    }).catch(function (error) {
                        console.error(error);
                    });
                }
                this.dialogDetalles =true;
            },

            // @vuese
            //Cierra todos los modales, dejando los alores por defecto de los datos que se usan en todos los modales
            funcionClose: function () {
                console.log("close");
                this.dialogCrear=false;
                this.opcion = null;
                this.hogar.apodo=null;
                this.listenerIniciado=false;
                this.dialog = false;
                this.dialogModificar = false;
                this.dialogBorrar=false;
                this.currentPlace=null;
                this.dialogDetalles = false;
                this.hogarModificarDireccion=null;
                this.hogarModificar=null;
                this.hogarModificarApodo=null;
                let usuarioID = firebase.auth().currentUser.uid;
                let db = firebase.firestore();
                let self = this;
                db.collection("Hogares").where("usuarioAsociado", "==", usuarioID).onSnapshot(function (querySnapshot) {
                    self.lista_hogares = [];//la vacio antes de volver a llenrala con la nueva
                    querySnapshot.forEach(function (doc) {
                        let hogar = {
                            id: doc.id,
                            ...doc.data()
                        }
                        self.lista_hogares.push(hogar)
                    });
                })
            },

            // @vuese
            //Redirecciona a la pagina de mostrar los detalles de un sensor
            botonDetallesSensor: function (dispositivo) {
                console.log("pulsado boton detalles del sensor " + dispositivo.identificacion)
                const parametro = dispositivo.identificacion
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.push({
                            name: 'DetallesDispositivo',
                            params: {idSensor: parametro, apodo: dispositivo.apodo, origen: "Dispositivos"}
                        })
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: ' + err.message)
                        console.log('Opps ' + err.message);
                    }
                );
            },

            // @vuese
            // Guarda la creación de un hogar
            funcionSave: function () {
                console.log("save");
                this.dialogCrear = false;
                this.opcion=null;
                this.dialog = false;
                this.hogar.direccion = this.places[0].formatted_address;
                console.log("Guardo y hogar.direccion tiene: "+this.hogar.direccion);
                //Primero añado el hogar a la colecciond de hogares (datos, mas id mas usuario relaccionado)
                let hogarRef = firebase.firestore().collection("Hogares").doc();
                hogarRef.set({
                    idHogar: hogarRef.id,
                    direccion: this.hogar.direccion,
                    apodo: this.hogar.apodo,
                    usuarioAsociado: firebase.auth().currentUser.uid,
                    vectorSensores: []
                });
                this.hogar.direccion = null;
                this.currentPlace=null;
                this.hogar.apodo = null;
            },

            // @vuese
            // Guarda la edición de un hogar
            funcionGuardarEdicion: async function () {
                let self = this;
                console.log("pulsado guardar edicion del hogar " + self.hogar.direccion);
                this.dialogModificar = false;
                this.dialogCrear = false;
                console.log("Nuevos valores en el hogar con id: "+ self.hogarModificar.idHogar +". Direccion->" + self.hogar.direccion + "  apodo->" + self.hogarModificarApodo)
                await firebase.firestore().collection('Hogares').doc(self.hogarModificar.idHogar).set({
                    direccion: self.hogar.direccion,
                    apodo: self.hogarModificarApodo
                }, {merge: true});

                console.log("Nuevo: "+self.hogarModificarApodo);
                console.log("Viejo: "+ self.opcion.apodo);
                //Tambien tengo que buscar los dispositivos de ese hogar y actualizarles el hogar al nuevo apodo

                await firebase.firestore().collection('Dispositivos').where("hogarAsociado","==", self.opcion.apodo).where("usuarioAsociado","==", firebase.auth().currentUser.uid)
                    .get().then(async function (querySnapshot) {
                        querySnapshot.forEach(async function (doc) {
                            console.log("Modifico sensor "+doc.data().apodo + " con nuevo hogarAsociado: "+self.hogarModificarApodo);
                            await firebase.firestore().collection("Dispositivos").doc(doc.data().identificacion).set({
                                hogarAsociado: self.hogarModificarApodo
                            },{merge:true});
                        })
                    }).catch(function(error) {
                        console.log("Error getting documents: ", error);
                    });

                console.log("Termino modificación");
                this.opcion=null;
            },

            // @vuese
            // Muestra los sensores asociados a un hogar
            //@param Hogar a examinar
            botonDetalles: function (hogar) {
                console.log("Pulsado el boton detalles ")
                let self = this
                this.lista_sensores_hogar = []
                console.log("hola 1 -->" + this.lista_sensores_hogar)
                for (let i = 0; i < hogar.vectorSensores.length; i++) {
                    firebase.firestore().collection('Dispositivos').doc(hogar.vectorSensores[i]).get().then(function (doc) {
                        console.log(doc.data())
                        self.lista_sensores_hogar.push(doc.data())
                        console.log("hola 2 -->" + self.lista_sensores_hogar)
                    }).catch(function (error) {
                        console.error(error);
                    });
                }
            },

            // @vuese
            // Abre el modal de borrar un hogar
            abrirDialogBorrar:function(hogar){
                this.hogarBorrarDireccion=hogar.direccion,
                    this.hogarBorrarApodo=hogar.apodo,
                    this.hogarBorrar=hogar;
                console.log("Hogar = "+hogar);
                this.dialogBorrar=true;
            },

            // @vuese
            //Borra un hogar
            //@arg Hogar a borrar
            botonBorrar: function (hogar) {
                let hogarAuxId = hogar.id;
                this.dialogBorrar=false;
                console.log("pulsado boton borrar el " + hogar.apodo + hogarAuxId)
                //Primero hay que borrar los sensores asociados al hogar
                //Recorro la lista de vector sensores del hogar primero
                //Segundo busco los sensores con ese id
                for (var i = 0; i < hogar.vectorSensores.length; i++) {
                    let idSensor = hogar.vectorSensores[i]
                    console.log("idSensor = " + idSensor)
                    firebase.firestore().collection("Dispositivos").doc(idSensor).delete()
                }
                firebase.firestore().collection("Hogares").doc(hogar.id).delete().then(function () {
                    console.log("Document successfully deleted!");
                }).catch(function (error) {
                    console.error("Error removing document: ", error);
                });
            }

        },
    }
</script>

<style scoped>
</style>
