<template>
    <div class="RecuperarContraseña ">
        <div class="text-center justify-center">
            <h3>Recuperar contraseña</h3>
            <v-form v-model="esValido" style="margin: 2px" >
                <v-text-field
                        v-model="email"
                        label="Escribe el email destino de la nueva contraseña"
                        counter
                        outlined
                        clearable
                ></v-text-field>
            </v-form>

            <v-btn :disabled="!esValido" @click="recuperar" rounded color="primary" >Enviar</v-btn>
            <br><br>
            <h3>Volver a <router-link style="color: blue" to = "/login">Iniciar sesión</router-link>.</h3>
        </div>
    </div>
</template>

<script>
    import  firebase from 'firebase'

    export default {
        name: "RecuperarContraseña",
        data(){
            return{
                esValido: false,
                email:'',
                contrasena:'',
                show3:false,
            }
        },
        methods:{

            // @vuese
            //Envia un email con la nueva contraseña
            recuperar: function () {
                var auth = firebase.auth();
                var emailAddress = this.email;
                auth.sendPasswordResetEmail(emailAddress).then(function() {
                    alert('Contraseña nueva enviada al correo');
                    this.$router.replace('login')

                }).catch(function(err) {
                    alert('Opps '+err.message)
                    console.log('Opps '+err.message);
                });
            }
        }
    }


</script>

<style scoped>

</style>
