<template>
    <div class="EditarInformacion ma-0 pa-0">
        <barra-superior></barra-superior>
        <v-row style="margin: 10px" >
            <v-list-item-avatar size="20%" @click="funcionHolaCaracola">
                <v-img ></v-img>
            </v-list-item-avatar>



        </v-row>
        <v-text-field style="margin-top: 60px"
                v-model="usuario.nombre"
                label="Nombre"
                outlined
                shaped
        ></v-text-field>
        <v-text-field
                v-model="usuario.apellido"
                label="Apellidos"
                outlined
                shaped
        ></v-text-field>

        <v-row >
            <v-col>
                <v-btn @click="funcionCancelar" rounded color="primary" style="color: aqua; background: red" >Cancelar</v-btn>
            </v-col>
            <v-col>
                <v-btn @click="funcionGuardar" rounded color="primary" style="color: aqua; background: green" >Guardar</v-btn>

            </v-col>
        </v-row>



    </div>
</template>

<script>
    import * as firebase from 'firebase';
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    export default {
        name: "EditarInformacion",
        components: {
            BarraSuperior
        },


        data(){
            return{
                nombre2: "algo",
                show3:false,
                usuario:
                    {
                        email: "",
                        password : "",
                        apellido: "",
                        nombre:"",
                        foto:null,
                        db: firebase.firestore()},
            }
        },
        label: 'algo!',
        created:function() {
            let db = firebase.firestore();
            db.collection("Usuarios").doc(firebase.auth().currentUser.uid).get().then( (doc) =>{
                    this.usuario.nombre=(doc.data().nombre)
                    this.usuario.email=(doc.data().email)
                    this.usuario.apellido=(doc.data().apellido)
                    this.usuario.email=(doc.data().email)
                    this.usuario.password=(doc.data().password)
            });
            var storage = firebase.storage();
            var gsReference = storage.refFromURL('gs://vue-firebase-tutorial-7ee46.appspot.com/'+firebase.auth().currentUser.uid);
            console.log("algo")
            console.log(gsReference)



        },
        methods:{

            // @vuese
            // Guarda los datos editados en el perfil del usuario
            funcionGuardar : function(){
                firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                    nombre: this.usuario.nombre,
                    apellido: this.usuario.apellido,
                    password: this.usuario.password
                }, {merge:true}).then(function () {
                }).catch(function (error) {
                    console.error("Error adding document: ", error);
                });


                //Redireccionamos a ir a perfil
                firebase.auth().signInWithEmailAndPassword(this.usuario.email,this.usuario.password).then(
                    (user)=> {
                        this.$router.replace('EditarPerfil')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: '+err.message)
                        console.log('Opps '+err.message);
                    }
                );

            },

            // @vuese
            // Cancela la edicion del perfil
            funcionCancelar:function() {
                console.log("Volvemos a perfil")
                this.$router.replace('EditarPerfil')
            }
            ,
        },
    }
</script>





