<script>

    import * as firebase from 'firebase';
    import BarraSuperior from "@/views/barraSuperior/BarraSuperior";
    import Chart from "chart.js";
    import "chartjs-plugin-zoom";

    export default {
        name: "DetallesDispositivo",
        components: {
            BarraSuperior
        }, props: {
            // @vuese
            //Contiene el identificador del sensor a detallar
            idSensor: {
                type: [String],
                required: true,
                // undefined
                default: undefined
            },

            // @vuese
            //Contiene el apodo del sensor a detallar
            apodo: {
                type: [String],
                required: true,
                // undefined
                default: undefined
            },

            // @vuese
            //Contiene el titulo usado en la barra
            origen: {
                type: [String],
                required: true,
                // undefined
                default: undefined
            },
        },

        data() {
            return {
                tipo: null,
                tiempo: null,
                numAlertasDay: [],
                dispositivo: null,
                horaCreated: null,
                dataReady: false,
                horaDatos: null,
                minutosDatos: null,
                historial: [],
                activaciones: [],
                myChartDay: null,
                myChartDayH: null,
                myChartDayP: null,
                myChartDay24hT: null,
                myChartDay24hH: null,
                myChartDay24hP: null,
                myCharDayWeekT: null,
                myCharDayWeekH: null,
                myCharDayWeekP: null,
                myChart24h: null,
                myChartSemana: null,
                loadingActivations: true,
                usuario:
                    {
                        dato1: null,
                        dato2: null,
                        email: null,
                        password: null,
                        apellido: null,
                        nombre: "null",
                        foto: null,
                        db: firebase.firestore()
                    }
                ,
            }

        },
        created: async function () {
            let db = firebase.firestore();
            let usuarioID = firebase.auth().currentUser.uid;
            this.horaCreated = new Date().getHours();
            let self = this;
            this.tipo = "T";
            this.tiempo = "D"
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre)
                self.usuario.email = (doc.data().email)
                self.usuario.apellido = (doc.data().apellido)
                self.usuario.password = (doc.data().password)
                self.usuario.foto = (doc.data().foto)
                console.log("Nombre: " + self.usuario.nombre)

                //Si es indefinido pues se va para dispositivos de nuevo
                console.log("Idsensor =" + self.idSensor)
                if (self.idSensor == undefined) {
                    console.log("na macho que no tiene valor asi que vuelvo a dispositivos")
                    firebase.auth().signInWithEmailAndPassword(self.usuario.email, self.usuario.password).then(
                        (user) => {
                            self.$router.replace('Dispositivos')
                            console.log(user)
                        },
                        function (err) {
                            alert('Error: ' + err.message)
                            console.log('Opps ' + err.message);
                        }
                    );
                }

            });

            //Primero cojo los datos del sensor sin contar el historial
            await db.collection("Dispositivos").doc(this.idSensor).get().then(function (doc) {
                if (doc.exists) {
                    console.log("Document data:", doc.data());
                    self.dispositivo = doc.data()
                    if (self.dispositivo != undefined && self.dispositivo != null) {
                        //self.generarGrafica()
                    }
                }
                console.log(self.dispositivo)

            }).catch(function (error) {
                console.log("Error getting document:", error);
            });

            //Despues ya cojo todo el historial
            await db.collection("Dispositivos").doc(this.idSensor).collection("Historial").onSnapshot(function (querySnapshot) {
                self.historial = [];//la vacio antes de volver a llenrala con la nueva
                querySnapshot.forEach(function (doc) {
                    let hogar = {
                        id: doc.id,
                        ...doc.data()
                    }
                    let myDate = new Date(hogar.time.seconds * 1000)
                    myDate.toLocaleString("es-ES");
                    hogar.time = myDate.toString()
                    self.historial.push(hogar)
                });
                self.historial.sort(function (a, b) {
                    if (new Date(a.time) > new Date(b.time)) {
                        return -1;
                    }
                    if (new Date(a.time) < new Date(b.time)) {
                        return 1;
                    }
                    return 0;
                });
                for (let i = 0; i < self.historial.length; i++) {
                    // console.log("Timepo: " + self.historial[i].time)
                }
                self.activaciones = [5]
            });
            this.loadingActivations = false;


        },
        watch: {
            // @vuese
            // Especifica cuanto tiempo se quiere representar en las gráficas
            tiempo: function () {
                this.generarGrafica();
            },

            // @vuese
            // Especifica que valor se quiere representar en las gráficas
            tipo: function () {
                this.generarGrafica();
            },

            // @vuese
            // Activa y carga las gráficas al entrar en los detalles del dispositivo
            activaciones: function () {
                if (!this.loadingActivations && this.dispositivo != null) {
                    this.generarGrafica()
                }
            }
        },

        computed: {
            // @vuese
            // Muestra el dia en formato calendario
            day() {
                var date = new Date();
                return (
                    date
                        .toLocaleDateString("es", {weekday: "long"})
                        .charAt(0)
                        .toUpperCase() +
                    date.toLocaleDateString("es", {weekday: "long"}).slice(1) +
                    " " +
                    date.getDate() +
                    " de " +
                    date.toLocaleDateString("es", {month: "long"}) +
                    " de " +
                    date.getFullYear()
                );
            },

            // @vuese
            // Muestra la semana en formato calendario
            week() {
                var date = new Date();
                date.setHours(0, 0, 0, 0);
                date.setDate(date.getDate() + 3 - ((date.getDay() + 6) % 7));
                var week1 = new Date(date.getFullYear(), 0, 4);
                return (
                    1 +
                    Math.round(
                        ((date.getTime() - week1.getTime()) / 86400000 -
                            3 +
                            ((week1.getDay() + 6) % 7)) /
                        7
                    )
                );
            },

            // @vuese
            // Muestra el año en formato calendario
            year() {
                var d = new Date();
                return d.getFullYear();
            },

        },


