<template>
    <div class="sign-up">
        <div align="center" style="padding-top: 30px">
            <v-img src="/img/icons/RobertoLogo.png" style="width: 70%; max-width: 400px"></v-img>
        </div>

        <h3>Crear cuenta</h3>

            <v-form  style="margin-left: auto;margin-right: auto; width: 70%">

            <v-text-field
                    ref="nombre"
                    v-model="nombre"
                    :rules="[rules.requiredNombre]"
                    label="Nombre"
                    counter
                    outlined
                    clearable
            ></v-text-field>
            <v-text-field
                    ref="apellido"
                    v-model="apellido"
                    :rules="[rules.requiredApellido]"
                    label="Apellidos"
                    counter
                    outlined
                    clearable
            ></v-text-field>
            <v-text-field
                    ref="email"
                    v-model="email"
                    label="Correo"
                    :rules="[rules.requiredEmail,rules.email]"
                    counter
                    outlined
                    clearable
            ></v-text-field>
            <v-text-field
                    ref="password"
                    v-model="password"
                    :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show3 ? 'text' : 'password'"
                    label="Contraseña"
                    class="input-group--focused"
                    :rules="[rules.requiredPassword, rules.min,rules.mismaContrasena]"
                    counter
                    outlined
                    @click:append="show3 = !show3"
            ></v-text-field>
            <v-text-field
                    ref="passwordAgain"
                    v-model="passwordAgain"
                    :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                    :type="show3 ? 'text' : 'password'"
                    label="Repite contraseña"
                    class="input-group--focused"
                    :rules="[rules.requiredPasswordAgain, rules.minAgain,rules.mismaContrasena]"
                    counter
                    outlined
                    @click:append="show3 = !show3"
            ></v-text-field>
        </v-form>

        <v-btn rounded  color="primary" style="color: aqua; background: red" @click="singUp">
            Registrarse
        </v-btn>
        <br>
        <h4> o volver a
            <router-link style="color: blue" to="/login">Iniciar sesión</router-link>
            .
        </h4>


    </div>
</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "SingUp",

        data() {//son los dos datos que se manejan, email y pasword
            return {
                datosValidos: false,
                pattern: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                email: "",
                foto: null,
                password: "",
                passwordAgain: "",
                combinacion: false,
                apellido: "",
                nombre: "",
                show3: false,

                //Validaciones
                nombreValido:true,
                apellidoValido:true,
                emailValido: true,
                passwordValido: true,
                passwordAgainValido:true,
                passwordsIgualesValido: true,


                rules:{
                    requiredNombre:() => this.nombreRequerido() || 'Campo requerido.',
                    requiredApellido:() => this.apellidoRequerido() || 'Campo requerido.',
                    requiredEmail: () => this.emailRequerido() || 'Campo requerido.',
                    requiredPassword: () => this.passwordRequerido() || 'Campo requerido.',
                    requiredPasswordAgain: () => this.passwordRequeridoAgain() || 'Campo requerido.',
                    min: () => this.contrasenaMinimo8() || 'Mínimo 8 carácteres',
                    minAgain: () => this.contrasenaMinimo8Again()||'Mínimo 8 carácteres',
                    email: () => {
                        return this.emailFormatoValido() || 'Email no válido.'
                    },
                    iguales: value=> value === this.password.valueOf() || 'La contraseñas no coinciden',
                    iguales2: value=> value === this.passwordAgain.valueOf() || 'La contraseñas no coinciden',
                    mismaContrasena:()=> this.passwordIgual() || 'Las contraseñas no coinciden'
                }

            }
        },

        watch:{
            // @vuese
            //Comprueba que el nombre sea valido
            nombre: function () {
                if (this.nombre && this.nombre.length > 0 ) {
                    this.nombreValido = true;
                }
            },
            // @vuese
            //Comprueba que el apellido sea valido
            apellido: function () {
                if (this.apellido && this.apellido.length > 0 ) {
                    this.apellidoValido = true;
                }
            },

            // @vuese
            //Comprueba que el email sea valido
            email: function () {
                if (this.email && this.email.length > 0 && this.pattern.test(this.email)) {
                    this.emailValido = true;
                }
            },

            // @vuese
            //Comprueba que la contraseña sea valida
            password: function (value) {
                if (this.password && this.password.length > 7 && this.password==this.passwordAgain) {
                    console.log("ENTRO")
                    this.passwordValido = true;
                }

                this.$refs.password.validate();
                this.$refs.passwordAgain.validate();

                if(value === this.passwordAgain){
                    this.combinacion =true;
                    this.$refs.passwordAgain.validate();
                    console.log(this.passwordAgain)
                }else{
                    console.log(this.passwordAgain)
                    this.combinacion =false;
                }
            },

            // @vuese
            //Comprueba que la contraseña repetida sea valida
            passwordAgain: function (value) {
                console.log("--->" + this.passwordAgainValido)
                if (this.passwordAgain && this.passwordAgain.length > 7 && this.password==this.passwordAgain && this.password == this.passwordAgain) {
                    this.passwordAgainValido = true;
                }

                this.$refs.password.validate();
                this.$refs.passwordAgain.validate();

                if(value === this.password){
                    this.combinacion =true;
                    this.$refs.password.validate();
                    console.log(this.password)
                }else{
                    console.log(this.password)
                    this.combinacion =false;
                }
            },

            // @vuese
            //Comprueba que la contraseña sea valida
            passwordIguales:function (value) {
                if(value === this.passwordAgain){
                    this.combinacion =true;
                    this.$refs.passwordAgain.validate();
                    console.log(this.passwordAgain)
                }else{
                    console.log(this.passwordAgain)
                    this.combinacion =false;
                }
            },

            // @vuese
            //Comprueba que la contraseña repetida sea valida
            passwordAgainIguales:function (value) {
                if(value === this.password){
                    this.combinacion =true;
                    this.$refs.password.validate();
                    console.log(this.password)
                }else{
                    console.log(this.password)
                    this.combinacion =false;
                }
            }

        },
        created: function () {
            this.nombre = '';
            this.apellido = '';
            this.passwordAgain = '';
            this.email = '';
            this.password = '';
        },

        methods: {

            // @vuese
            //Comprueba que el email no falte a la hora de registrarse
            emailRequerido: function () {
                if (this.emailValido) {
                    return true;
                } else {
                    if(this.email != null && this.email.length > 0){
                        return true;
                    }
                    else{
                        console.log("emailr")
                        return false;
                    }
                }
            },

            // @vuese
            //Comprueba que el nombre no falte a la hora de registrarse
            nombreRequerido:function(){
                if (this.nombreValido) {
                    return true;
                } else {
                    if(this.nombre != null && this.nombre.length > 0){
                        return true;
                    }
                    else{
                        console.log("nombre")
                        return false;
                    }
                }
            },

            // @vuese
            //Comprueba que el apellido no falte a la hora de registrarse
            apellidoRequerido:function(){
                if (this.apellidoValido) {
                    return true;
                } else {
                    if(this.apellido != null && this.apellido.length > 0){
                        return true;
                    }
                    else{
                        console.log("apellido")
                        return false;
                    }
                }
            },

            // @vuese
            //Comprueba que la contraseña no falte a la hora de registrarse
            passwordRequerido: function () {
                if (this.passwordValido) {
                    return true;
                } else {
                    let valid = this.password != null && this.password.length > 0;
                    return valid;
                }
            },

            // @vuese
            //Comprueba que la contraseña repetida no falte a la hora de registrarse
            passwordRequeridoAgain: function () {
                if (this.passwordAgainValido) {
                    return true;
                } else {
                    let valid = this.passwordAgain != null && this.passwordAgain.length > 0;
                    return valid;
                }
            },

            // @vuese
            //Comprueba que la contraseña tenga mínimo 8 carácteres
            contrasenaMinimo8: function () {
                if (this.passwordValido) {
                    return true;
                } else {
                    return this.password.length >= 8;
                }
            },

            // @vuese
            // Comprueba que la contraseña repetida tenga mínimo 8 carácteres
            contrasenaMinimo8Again: function () {
                if (this.passwordAgainValido) {
                    return true;
                } else {
                    return this.passwordAgain.length >= 8;
                }
            },

            // @vuese
            //Comprueba que el email tenga formato válido
            emailFormatoValido: function () {
                if (this.emailValido) {
                    return true;
                } else {
                    return this.pattern.test(this.email);
                }
            },

            // @vuese
            //Comprueba que las contraseñas sean iguales
            passwordIgual:function(){
                if(this.passwordsIgualesValido){
                    console.log("PASS IGUALES VALIDO")
                    return true;
                }else{
                    console.log("PASS IGUALES NO VALIDO")
                    return this.password==this.passwordAgain;
                }
            },

            // @vuese
            //Registra al ususario con los datos introducidos
            singUp: function () {

                if (!this.nombre || this.nombre.length == 0 ) {
                    this.nombreValido = false;
                }
                if (!this.apellido || this.apellido.length == 0 ) {
                    this.apellidoValido = false;
                }
                if (!this.email || this.email.length == 0 || !this.pattern.test(this.email)) {
                    this.emailValido = false;
                }
                if (!this.password || this.password.length < 8 || this.password != this.passwordAgain) {
                    console.log("pass mal")
                    this.passwordValido = false;
                    this.passwordsIgualesValido = false;
                }
                if (!this.passwordAgain || this.passwordAgain.length < 8 || this.password != this.passwordAgain) {
                    console.log("passagain mal")
                    this.passwordAgainValido = false;
                    this.passwordsIgualesValido = false;
                }

                if (!this.nombreValido || !this.apellidoValido || !this.emailValido || !this.passwordValido || !this.passwordAgainValido) {
                    this.$refs.nombre.validate();
                    this.$refs.apellido.validate();
                    this.$refs.email.validate();
                    this.$refs.password.validate();
                    this.$refs.passwordAgain.validate();
                    return;
                }


                let self = this;
                if(this.password.valueOf() === this.passwordAgain.valueOf()){
                    firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(
                        async (user) => {


                            await firebase.auth().currentUser.sendEmailVerification().then(function() {
                                console.log("Si verifico el email")
                                //cierro sesion
                                //Le envio a una pagina que pogapara verificar sesion, entra al correo
                                //Le pones un enlace a inciar sesion
                                //SI el email esta verificado, lo meto en el home
                                //si no esta verificado, le envío a la pagina que dice que verifique el email
                            }).catch(function() {
                                console.log("No verifico el email")
                            });




                            //intento guardar los datos del usuario en firestone
                            console.log(user)
                            console.log('-->' + firebase.auth().currentUser.uid)
                            firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).set({
                                apellido: this.apellido,
                                nombre: this.nombre,
                                email: this.email,
                                password: this.password,
                                primeraVez:true,
                                foto: null,
                                tokens: [],
                            }, {merge: true}).then(function () {//con el merge, los nuevos datos que se añadan con nuesvos dato1, se sumaran a la laista, los que ya estan, se actualziaran
                                console.log("Documento escrito con id : ", firebase.auth().currentUser.uid + " de " + self.email);
                                firebase.auth().signInWithEmailAndPassword(self.email, self.password).then(
                                    (user) => {
                                        // eslint-disable-next-line no-console
                                        console.log(user);
                                        //alert('estas ahora logueado, '+self.email);
                                        self.$router.replace('home')
                                    },
                                    function (err) {
                                        alert('Error: ' + err.message)
                                        // eslint-disable-next-line no-console
                                        console.log('Opps ' + err.message);
                                        console.log(user)
                                    }
                                );

                            }).catch(function (error) {
                                console.error("Error adding document: ", error);
                            });
                            let db = firebase.firestore();
                            db.collection("Usuarios").get().then((querySnapshot) => {
                                querySnapshot.forEach((doc) => {
                                    console.log(doc.data().email)
                                });
                            });

                        },
                        function (err) {
                            alert('Opps ' + err.message)
                        }
                    );

                }else{
                    alert('La contraseña repetida no coincide con la original. Revisa los datos introducidos.')
                }

            }
        }
    }
</script>

<style scoped>

</style>
