<template>
    <v-container
            class="mx-auto overflow-hidden ma-0 pa-0"
    >
        <v-app-bar
                color="#05c5ff"
                dark
                app
        >
            <v-app-bar-nav-icon @click="drawer = true"></v-app-bar-nav-icon>

            <v-toolbar-title id='titulo-nombre'>
                {{titulo}}
            </v-toolbar-title>

        </v-app-bar>

        <v-navigation-drawer
                v-model="drawer"
                fixed
                temporary
                style="width: auto;"
        >
            <v-list
                    nav
                    dense
            >
                <v-list-item-group active-class="blue--text text--accent-4">
                    <v-list-item style="background: lightskyblue" @click="funcionEditarPerfil" class="px-2"
                                 v-if="this.titulo === 'PERFIL'">
                        <v-icon>mdi-account-outline</v-icon>
                        <v-list-item-title>PERFIL</v-list-item-title>
                    </v-list-item>
                    <v-list-item v-else @click="funcionEditarPerfil" class="px-2">
                        <v-icon>mdi-account-outline</v-icon>
                        <v-list-item-title>PERFIL</v-list-item-title>
                    </v-list-item>


                    <v-list-item @click="funcionHome" v-if="this.titulo == 'Home'" style="background: lightskyblue">
                        <v-list-item-icon>
                            <v-icon>{{ home.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ home.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item @click="funcionHome" v-else>
                        <v-list-item-icon>
                            <v-icon>{{ home.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ home.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>

                    <v-list-item @click="funcionHogares" v-if="this.titulo == 'Hogares'"
                                 style="background: lightskyblue">
                        <v-list-item-icon>
                            <v-icon>{{ hogares.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ hogares.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item @click="funcionHogares" v-else>
                        <v-list-item-icon>
                            <v-icon>{{ hogares.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ hogares.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>


                    <v-list-item @click="funcionDsipositivos" v-if="this.titulo == 'Dispositivos'"
                                 style="background: lightskyblue">
                        <v-list-item-icon>
                            <v-icon>{{ dispositivos.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ dispositivos.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item @click="funcionDsipositivos" v-else>
                        <v-list-item-icon>
                            <v-icon>{{ dispositivos.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ dispositivos.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>


                    <v-list-item @click="funcionAlertas" v-if="this.titulo == 'Alertas'"
                                 style="background: lightskyblue">
                        <v-list-item-icon>
                            <v-icon>{{ alertas.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ alertas.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>
                    <v-list-item @click="funcionAlertas" v-else>
                        <v-list-item-icon>
                            <v-icon>{{ alertas.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ alertas.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>


                    <v-list-item @click="funcionCerrarSesion">
                        <v-list-item-icon>
                            <v-icon>{{ cerrarSesion.icon }}</v-icon>
                        </v-list-item-icon>
                        <v-list-item-content>
                            <v-list-item-title>{{ cerrarSesion.title }}</v-list-item-title>
                        </v-list-item-content>
                    </v-list-item>


                </v-list-item-group>
            </v-list>
        </v-navigation-drawer>
    </v-container>
</template>

<script>
    import * as firebase from "firebase";

    export default {
        props: {
            // @vuese
            //Contiene el titulo usado en la barra
            titulo: {
                type: [String],
                required: true,
                default: null
            },

            //Contiene el token del usuario
            token: {
                type: [String],
                required: false,
                default: null
            },

        },
        name: "BarraSuperior",
        data: () => ({
            usuario:
                {
                    dato1: null,
                    dato2: null,
                    email: null,
                    password: null,
                    apellido: null,
                    nombre: "null",
                    foto: null,
                    tokens: [],
                    primeraVez: null,
                    db: firebase.firestore()
                }
            ,
            home: {
                title: 'Home',
                icon: 'mdi-home-outline',
                clickFuncion: 'funcionHome'
            },
            hogares: {
                title: 'Hogares',
                icon: 'mdi-home-city-outline',
                clickFuncion: 'funcionHogares'
            },
            dispositivos: {
                title: 'Dispositivos',
                icon: 'mdi-leak',
                clickFuncion: 'funcionDispositivos'
            },
            alertas: {
                title: 'Notificaciones',
                icon: 'mdi-alarm-light-outline',
                clickFuncion: 'funcionAlertas'
            },
            cerrarSesion: {
                title: 'Cerrar Sesión',
                icon: 'mdi-close-octagon-outline',
                clickFuncion: 'funcionCerrarSesion'
            },

            drawer: false,
        }),
        created: async function () {
            let db = firebase.firestore();
            let usuarioID = firebase.auth().currentUser.uid;
            let self = this;
            console.log(usuarioID);
            await db.collection("Usuarios").doc(usuarioID).get().then(function (doc) {
                self.usuario.nombre = (doc.data().nombre);
                self.usuario.email = (doc.data().email);
                self.usuario.apellido = (doc.data().apellido);
                self.usuario.primeraVez = (doc.data().primeraVez);
                self.usuario.password = (doc.data().password);
                self.usuario.foto = (doc.data().foto);
                self.usuario.tokens = (doc.data().tokens);
                console.log("Nombre: " + self.usuario.nombre)
            });
        },

        methods: {
            // @vuese
            //Redirecciona al la pagina Home
            funcionHome: function () {
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.replace('home')
                        console.log(user)
                    },
                    function (err) {
                        console.log('Opps ' + err.message);
                    }
                );


            },
            // @vuese
            //Redirecciona al la pagina Hogares
            funcionHogares: function () {
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.replace('Hogares')
                        console.log(user)
                    },
                    function (err) {
                        console.log('Opps ' + err.message);
                    }
                );

            },
            // @vuese
            //Redirecciona al la pagina Dispositivos
            funcionDsipositivos: function () {
                console.log("voy a dispositivos")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.replace('Dispositivos')
                        console.log(user)
                    },
                    function (err) {
                        console.log('Opps ' + err.message);
                    }
                );
            },
            // @vuese
            //Redirecciona al la pagina Alertas
            funcionAlertas: function () {
                console.log("voy a alertas")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        this.$router.replace('Alertas')
                        console.log(user)
                    },
                    function (err) {
                        console.log('Opps ' + err.message);
                    }
                );

            },
            // @vuese
            //Redirecciona al la pagina Login
            funcionCerrarSesion: async function () {
                console.log("AppName tiene: " + this.$appName);
                for (let i = 0; i < this.usuario.tokens.length; i++) {
                    if (this.usuario.tokens[i] == this.$appName) {
                        console.log("Borro el token: " + this.usuario.tokens[i]);
                        this.usuario.tokens.splice(i, 1);

                        //Cargo el nuevo lista de tokens
                        await firebase.firestore().collection("Usuarios").doc(firebase.auth().currentUser.uid).update({
                            tokens: this.usuario.tokens,
                        }).then(function () {
                            console.log("escrito")
                        }).catch(function (error) {
                            console.error("Error adding document: ", error);
                        });
                    }
                }
                console.log("Salgo, y el token que tengo es: " + this.token + " (Ya deberia estar borrado)");
                firebase.auth().signOut().then(() => {
                    this.$router.replace('login');
                })

            },
            // @vuese
            //Redirecciona al la pagina de editar perfil
            funcionEditarPerfil: function () {
                console.log("funcion ir perfil")
                firebase.auth().signInWithEmailAndPassword(this.usuario.email, this.usuario.password).then(
                    (user) => {
                        //alert('estas ahora logueado');
                        this.$router.replace('EditarPerfil')
                        console.log(user)
                    },
                    function (err) {
                        alert('Error: ' + err.message)
                        console.log('Opps ' + err.message);
                    }
                );
            },
        },

    }
</script>

<style scoped>

</style>
